import { EventEmitter } from 'events';

export const MIX = {
  VIDEO: 'MIX:VIDEO',
};

export const LIVE = {
  JOIN_VIDEO: 'LIVE:JOIN_VIDEO',
  JOIN_VIDEO_CANCEL: 'LIVE:JOIN_VIDEO_CANCEL',
  AUDIO: {
    ENABLE: 'LIVE:AUDIO:ENABLE',
    DISABLE: 'LIVE:AUDIO:DISABLE'
  },
  VIDEO: {
    ENABLE: 'LIVE:VIDEO:ENABLE',
    DISABLE: 'LIVE:VIDEO:DISABLE'
  },
  STREAM_ADD_SUCCESS: 'LIVE:STREAM_ADD_SUCCESS'
};

export const P2P = {
  START: 'P2P:START', // execute start
  CLOSE: 'P2P:CLOSE', // execute close
  REQUEST_TOKEN: 'P2P:REQUEST_TOKEN',
  RESPONSE_TOKEN: 'P2P:RESPONSE_TOKEN',
  ONENTER: 'P2P:ONENTER',
  ONMESSAGE: 'P2P:ONMESSAGE',
  ONTRACK: 'P2P:ONTRACK',
  ONCLOSE: 'P2P:ONCLOSE', // closed
  ONERROR: 'P2P:ONERROR',
};

const cojamEvent = new EventEmitter();

export const listen = (type, listener) => cojamEvent.on(type, listener);
export const listenOnce = (type, listener) => cojamEvent.once(type, listener);
export const produce = (type, data) => cojamEvent.emit(type, data);

export default cojamEvent;
