import POPUP from '../../../common_ui/popup';
import DragElement from '../../../common_ui/DragElement';
import BytesToSize from '../../../utils/BytesToSize';
import LiveService from '../../../services/live.service';
export default (() => {
    let $stats_monitor_popup = null;
    let $rtc_info_list = null;
    let _onoffState = false;
    let _rtcPeerconnection = null;
    let getStatsResult = null;
    let _startTime = null;
    let _intVal_trackInfo = null;

    const MONITOR_INTERVAL = 2000;
    const init = () => {
        $stats_monitor_popup = $('#popup-stats-monitor');
        DragElement($stats_monitor_popup.find('.popup').get(0))
        $stats_monitor_popup.hide();
        $rtc_info_list = $stats_monitor_popup.find('#rtc-info');

        bindEventHandler();

        checkRTC();
    }

    const checkRTC = () => {
        DetectRTC.load(function () {
            addRTCinfo(`hasWebCAM : ${DetectRTC.hasWebcam}`)// (has webcam device!)
            addRTCinfo(`hasMIC : ${DetectRTC.hasMicrophone}`); // (has microphone device!)
            addRTCinfo(`hasSpeakers : ${DetectRTC.hasSpeakers}`); // (has speakers!)
            addRTCinfo(`Supported Screen Capture : ${DetectRTC.isScreenCapturingSupported}`); // Chrome, Firefox, Opera, Edge and Android
            addRTCinfo(`Supported Sctp DataChannel : ${DetectRTC.isSctpDataChannelsSupported}`);
            addRTCinfo(`Supported Rtp Datachannel : ${DetectRTC.isRtpDataChannelsSupported}`);
            addRTCinfo(`Supported AudioContext : ${DetectRTC.isAudioContextSupported}`);
            addRTCinfo(`Supported WebRTC : ${DetectRTC.isWebRTCSupported}`);
            addRTCinfo(`Supported Desktop Capturing : ${DetectRTC.isDesktopCapturingSupported}`);
            addRTCinfo(`isMobile : ${DetectRTC.isMobileDevice}`);
            
            addRTCinfo(`Supported WebSocket : ${DetectRTC.isWebSocketsSupported}`);
            addRTCinfo(`Blocked WebSocket : ${DetectRTC.isWebSocketsBlocked}`);
            // DetectRTC.checkWebSocketsSupport(function() {
            // })

            addRTCinfo(`WebCAM Permission : ${DetectRTC.isWebsiteHasWebcamPermissions}`);        // getUserMedia allowed for HTTPs domain in Chrome?
            addRTCinfo(`WebMIC Permission : ${DetectRTC.isWebsiteHasMicrophonePermissions}`);    // getUserMedia allowed for HTTPs domain in Chrome?

            addRTCinfo(`Audio Input Device : ${getDeviceLabels(DetectRTC.audioInputDevices)}`);    // microphones
            addRTCinfo(`Audio Output Device : ${getDeviceLabels(DetectRTC.audioOutputDevices)}`);   // speakers
            addRTCinfo(`Video Input Device : ${getDeviceLabels(DetectRTC.videoInputDevices)}`);    // cameras

            addRTCinfo(`OS : ${DetectRTC.osName}`);
            addRTCinfo(`OS Version : ${DetectRTC.osVersion}`);

            addRTCinfo(`Browser : ${DetectRTC.browser.name === 'Edge' || 'Chrome' || 'Firefox'}`);
            addRTCinfo(`Browser Version : ${DetectRTC.browser.version}`);
            addRTCinfo(`- isChrome : ${DetectRTC.browser.isChrome}`);
            addRTCinfo(`- isFirefox : ${DetectRTC.browser.isFirefox}`);
            addRTCinfo(`- isOpera : ${DetectRTC.browser.isOpera}`);
            addRTCinfo(`- isIE : ${DetectRTC.browser.isIE}`);
            addRTCinfo(`- isSafari : ${DetectRTC.browser.isSafari}`);
            addRTCinfo(`- isEdge : ${DetectRTC.browser.isEdge}`);

            addRTCinfo(`Browser PrivateMode : ${DetectRTC.browser.isPrivateBrowsing}`); // incognito or private modes

            addRTCinfo(`Supported Canvas Stream Capture : ${DetectRTC.isCanvasSupportsStreamCapturing}`);
            addRTCinfo(`Supported Video Stream Capture : ${DetectRTC.isVideoSupportsStreamCapturing}`);

            // addRTCinfo(`IP Addr : ${DetectRTC.DetectLocalIPAddress(callback)}`);
        });
    }

    const addRTCinfo = (text) => {
        $rtc_info_list.append(`<li>${text}</li>`)
    }

    const getDeviceLabels = (devices) => {
        let ret = "";
        devices.map( (device, index) => {
            ret = ret + `${device.label}${index !== devices.length - 1 ? '    |    ' : ''}`;
        })

        return ret;
    }

    const setRtcConnection = (rtcPeerconnection) => {
        _rtcPeerconnection = rtcPeerconnection;
        _startTime = Date.now();
    }

    const open = () => {
        POPUP.open($stats_monitor_popup)
        start();
    }

    const bindEventHandler = () => {
        $stats_monitor_popup.find('.popup').drag
        $stats_monitor_popup.find('#btn-stat-onoff').click( function(e) {
            if(!_onoffState) {
                start();
            } else {
                stop();
            }
        })

        $stats_monitor_popup.find('.btn-popup-cancel').click(e => {
            stop();
        })

        $stats_monitor_popup.find('.btn-popup-ok').click(e => {
            confirm("개선된 서비스를 위해 사용자 정보를 전송합니다. 전송 하시겠습니까?",
            () => {
                // ok
                LiveService.sendReport(makeReport());
                stop();
                POPUP.close();
            },
            () => {
                // cancel
            })
        })
    }

    let packetsLost = {
        audio: 0,
        video: 0
    }
    const start = (e) => {
        if(!_rtcPeerconnection) return;
        getStats(_rtcPeerconnection, (result) => {
            getStatsResult = result;
            $('#stat-start-time').text(_startTime);
            $('#stat-current-time').text(Date.now());

            $('#stat-bandwidth').html(`Send:${result.bandwidth.googAvailableSendBandwidth}<br>Download:${result.bandwidth.speed}`);
            $('#stat-stun').text(result.connectionType.remote.candidateType);
            const displayCodecs = result.audio.send.codecs + '<br>' + result.video.send.codecs;
            $('#stat-codecs').html(displayCodecs);
            $('#stat-res').text(result.resolutions.send.width + 'x' + result.resolutions.send.height);
            $('#stat-encrypt').text(result.encryption);
            
            $('#stat-data').text("A : " + BytesToSize(result.audio.bytesSent) + " V : " + BytesToSize(result.video.bytesSent));
            $('#stat-lat').html(`A : ${result.audio.latency}<br>V : ${result.video.latency}`);
            
            result.results.forEach(function (item) {
                if (item.type === 'ssrc' && item.transportId === 'Channel-0-1') {
                    if (item.mediaType === 'audio' && /send/gi.test(item.id)) {
                        // console.log('audio')
                        // console.log(item)
                        packetsLost.audio = item.packetsLost
                    }
                    
                    if(item.mediaType === 'video' && /send/gi.test(item.id)) {
                        // console.log('video')
                        // console.log(item)
                        packetsLost.video = item.packetsLost
                    }
                }
            })
            $('#stat-packet').html(`A: ${packetsLost.audio}<br>V : ${packetsLost.video}`);
        }, MONITOR_INTERVAL);

        _intVal_trackInfo = setInterval(() =>
            _rtcPeerconnection.getStats(null).then(stats => {
                let statsOutput = "";
                stats.forEach(report => {
                    statsOutput += `<h2>Report: ${report.type}</h3>\n<strong>ID:</strong> ${report.id}<br>\n` +
                        `<strong>Timestamp:</strong> ${report.timestamp}<br>\n`;

                    // Now the statistics for this report; we intentially drop the ones we
                    // sorted to the top above
                    if (report.type === "track") {

                        Object.keys(report).forEach(statName => {
                            if (statName === "Timestamp") {
                                statsOutput += `<strong>${statName}:</strong> ${report[statName]}<br>\n`;
                            }
                        });
                    }
                });

                document.querySelector(".stats-box").innerHTML = statsOutput;
            })
            , MONITOR_INTERVAL);
        $('#btn-stat-onoff').html('STOP');
        _onoffState = true;

    }

    const stop = () => {
        if (_onoffState) {
            getStatsResult.nomore();
        }

        if (_intVal_trackInfo) {
            clearInterval(_intVal_trackInfo)
        }
        $('#btn-stat-onoff').html('START');
        _onoffState = false;

    }

    const makeReport = () => {
        let report = "";
        function addText(text) {
            report = report + (text ? text : "") + '\n';
            return report;
        }
        addText(USER_SESSION.user_id);
        $.map($rtc_info_list.find('li'), function(item) {
            // console.log($(item).text())
            addText($(item).text());
        })

        if (_onoffState) {
            addText(`=============== Live Info ===============`);
            addText(`Bandwidth : ${$('#stat-bandwidth').html().replace('<br>', '\n')}`);
            addText(`STUN/TURN : ${$('#stat-stun').text()}`);
            addText(`Codecs : ${$('#stat-codecs').html().replace('<br', '\n')}`);
            addText(`Resolution : ${$('#stat-res').text()}`);
            addText(`Encryption : ${$('#stat-encrypt').text()}`);
            addText(`Data : ${$('#stat-data').text()}`);
            addText(`PacketLost : ${$('#stat-packet').text()}`)
        }
        // console.log(report)
        // LiveService.sendReportTest();
        return report;
    }

    return {
        init, open, setRtcConnection
    }
})()