export default function DeviceList(btn, contextMenu, position) {
    let _$btn = btn;
    let _$contextMenu = contextMenu;
    let _selectedItemIdx = 1;
    let _enabled = false;
    
    bindEventHandler();


    function setEnable(val) {
        _enabled = val;
    }

    function showToggle() {
        setTimeout(e => {
            if (_$contextMenu && _$contextMenu.fadeToggle) {
                _$contextMenu.fadeToggle(150);
            } else {
                _$contextMenu.toggle();
            }
        }, 10)
    };


    function hide() {
        setTimeout(e => {
            if (_$contextMenu && _$contextMenu.fadeOut) {
                _$contextMenu.fadeOut(200);
            } else {
                _$contextMenu.hide();
            }
        }, 10)
    }


    function bindEventHandler() {
        $('html').click((e) => {
            if (!$(e.target).closest(_$btn).is(_$btn)) {
                hide();
            }
        })
        $(_$btn).click( () => {
            if(_enabled)
                showToggle();
        })
    }

    function selectItem(id) {
        const $ul = $(_$contextMenu).find('ul');
        const $item = $(_$contextMenu).find(`li[data-device-id="${id}"]`);
        $ul.find('li').removeClass('active');
        $item.addClass('active')
    }

    function addItem(content, event) {
        const $ul = $(_$contextMenu).find('ul');
        if ($ul.append) {
            const item = $(content);
            $ul.append(item);
            if(item.click) {
                item.click((e) => {
                    selectItem(item.attr('data-device-id'))
                    event(e)
                })
            }
        }
    }

    return {
        addItem,
        setEnable,
        selectItem
    }
}