class Chat {
    constructor() {
      this.$chat_container = null;
      this.$msg_list = null;
      this.$input_msg = null;
      this.$btn_send_msg = null;

      this.sendEvent = null;

      this.senderInfo = null;
    }

    init(selectQuery, sendEvent, senderInfo) {
      const $chat_container = $(selectQuery);

      this.$chat_container = $chat_container;
      this.$msg_list = $chat_container.find('.msg-list');
      this.$input_msg = $chat_container.find('.chat-input-container .input-msg');
      this.$btn_send_msg = $chat_container.find('.chat-input-container .btn-send-msg');

      this.senderInfo = senderInfo;
      this.sendEvent = sendEvent;
      this.bindEventHandler();
    }

    bindEventHandler() {
      this.$btn_send_msg.click( (e) => {
        e.preventDefault();
        let msg = this.$input_msg.val();
        this.$input_msg.val('');
        if (msg == '') {
          return false;
        }
        this.sendMessage(msg);
      });
      
      this.$input_msg.keyup( (e) => {
        e.preventDefault();
        var $this = $(e.target);
        if (e.which != 13) {
          return false;
        }
      
        let msg = $this.val();
        $this.val('');
        if (msg == '') {
          return false;
        }
        this.sendMessage(msg);
      });


      this.$input_msg.click( e => {
        if(!this.senderInfo) location.href = "/login"
      })
      
    }


    appendMessage(avatar, name, msg) {
      this.$msg_list.append(`
        <li class="msg-item">
          <div class="avatar-container">
            <div class="avatar-wrapper">
              <img src="${avatar}" alt="${name}" />
            </div>
          </div>
          <div class="msg-container">
            <span class="nickname">${name}</span>
            ${msg}
          </div> 
        </li>
      `);

      this.chatWrapScroll();
    }

    sendMessage(msg) {
      // 메세지가 전송되는 로직이 완료되어야 채팅리스트에 내용 추가됨
      this.sendEvent(msg);
      this.appendMessage(this.senderInfo.avatar, this.senderInfo.name, msg)
    }

    chatWrapScroll() {
      $('.msg-list-container').scrollTop(
        9999
      );
    }
    
}

export default Chat;