import Fetch from '../utils/Fetch';

export default (() => {

  const getUploadUrl = (userSeq, title, desc, fileName, contentLength) => {
      return Fetch({
          url: `/api/v1/vod`,
          type: 'POST',
          data: JSON.stringify({
          "vod": {
            user_seq: userSeq,
            title,
            description: desc,
          },
          "file_upload_vo": {
            "upload_service_type": "vod",
            "file_type": "vod",
            "file_name": fileName,
            "content_length": contentLength
          }
        })
      })
  }

  let xhr = null;
  const cancelUploadVideo = () => {
      if(xhr.abort) {
        xhr.abort();
      }
  }
  const uploadVideo = (url, data, progressEvent) => {
      return new Promise((resolve, reject) => {
          xhr = $.ajax({
            url,
            type: 'PUT',
            headers: {
              'x-amz-acl': 'authenticated-read'
              // ...header,
              // ...meta
            },
            contentType: 'binary/octet-stream',
            processData: false,
            data,
            xhr: function () {
              var xhr = new window.XMLHttpRequest();
              //Upload progress
              xhr.upload.addEventListener("progress", function (evt) {
                if (evt.lengthComputable) {
                  if (progressEvent) {
                    var percentComplete = (evt.loaded / evt.total);
                    progressEvent(percentComplete);
                  }
                }
              }, false);
              return xhr;
          },
            success: function (res) {
              xhr = null;
              return resolve(res);
            },
            error: function (err) {
              xhr = null;
              console.log("upload error")
              return reject(err);
            }
          })

      })

  }

  const downloadVideo = (url, progressEvent) => {
    return new Promise((resolve, reject) => {
      xhr = $.ajax({
        url,
        type: 'GET',
        contentType: 'binary/octet-stream',
        processData: false,
        xhr: function () {
          var xhr = new window.XMLHttpRequest();
          //Upload progress
          xhr.addEventListener("progress", function (evt) {
            if (evt.lengthComputable) {
              if (progressEvent) {
                var percentComplete = (evt.loaded / evt.total);
                progressEvent(percentComplete);
              }
            }
          }, false);
          return xhr;
        },
        success: function (res) {
          xhr = null;
          return resolve(res);
        },
        error: function (err) {
          xhr = null;
          console.log("upload error")
          return reject(err);
        }
      })

    })

  }

  const getVideoList = (start, count, order, userSeq) => {
    return Fetch({
      url: `/api/v1/vod/${userSeq}/${count}/${start}/${order}?t=${Math.random()}`,
      cache: false
    })
  }

  const addViewCount = (videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/view/cnt/${videoSeq}`,
      type: 'PUT'
    })
  }

  const getVideoInfo = (videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/${videoSeq}`
    })
  }

  const modifyVideo = (videoSeq, title, desc) => {
    return Fetch({
      url: `/api/v1/vod/modify`,
      type: 'PUT',
      data: JSON.stringify({
        vod_seq: videoSeq,
        title,
        description: desc
      })
    })
  }

  const deleteVideo = (videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/${videoSeq}`,
      type: 'DELETE'
    })
  }


  const getLikeMe = (videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/like/check/${videoSeq}`,
      type: 'GET'
    })
  }


  const like = (videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/like/01/${videoSeq}`,
      type: 'PUT',

    })
  }

  const dislike = (videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/like/02/${videoSeq}`,
      type: 'PUT',

    })
  }

  const getJoinListAtVideo = (videoSeq, start, count) => {
    return Fetch({
      url: `/api/v1/vod/join/${videoSeq}/${count}/${start}`
    })
  }

  const cancelJoin = (channelSeq, videoSeq) => {
    return Fetch({
      url: `/api/v1/vod/join/${channelSeq}/${videoSeq}`,
      type: 'DELETE'
    })
  }

  const setFirstVideo = (channelSeq, vodSeq) => {
    return Fetch({
      url: `/api/v1/channel/firstvod/${channelSeq}/${vodSeq}`,
      type: 'PUT'
    })
  }

  return {
    getUploadUrl, uploadVideo, downloadVideo, cancelUploadVideo,
    getVideoList,
    addViewCount,
    getVideoInfo,
    modifyVideo, deleteVideo,
    getLikeMe, like, dislike,
    getJoinListAtVideo, cancelJoin,
    setFirstVideo
  }
})()