export const SCREEN = {
    width: 1280,
    height: 720
}

const MARGIN = {
    top: 10,
    left: 10,
    right: 10,
    bottom: 10
}

const SIZE = {
    SMALL: {
        width: SCREEN.width / 4,
        height: SCREEN.height / 4
    },
    MIDDLE: {
        width: SCREEN.width / 3,
        height: SCREEN.height / 3
    },
    LARGE: {
        width: SCREEN.width / 2,
        height: SCREEN.height / 2
    }
}

