function getHorizontalRight(objWidth) {
    return SCREEN.width - objWidth - MARGIN.right;
}

function getHorizontalCenter(objWidth) {
    return (SCREEN.width / 2) - (objWidth / 2);
}

function getVerticalBottom(objHeight) {
    return SCREEN.height - objHeight - MARGIN.bottom;
}

function getVerticalCenter(objHeight) {
    return (SCREEN.height / 2) - (objHeight / 2);
}