import DragElement from './DragElement';
import POPUP from './popup';
import { PRESET_POSITION } from '../pages/live/POSITIONS';
export default (() => {
    let $popup_container = null;
    let $video_player = null;
    let _objID = null;
    let _onOkEvent = null;
    let _onUpdateEvent = null;
    let _selectedScreen = "SCREEN_SMALL";
    let _selectedPosition = "TOP_LEFT";

    const open = (src, objID, defaultPosition, onOK, onUpdate) => {
        // 없으면 body에 팝업 생성, 있으면 source만 변경
        if ($('#popup-video-player').length === 0) {
            $('body').append(`
                <div id="popup-video-player" class="popup-container">
                    <div class="popup">
                        <div class="title fs-l bold">${i18next.t('show_video')}</div>
                        <div class="player-wrapper">
                            <video id="player-in-popup" controls autoplay></video>
                        </div>
                        <div class="title fs-l bold" data-i18n="sel_layout">${i18next.t('mnu_layout')}</div>
                        <label class="sub-title fs-s bold">${i18next.t('scr_size')}</label>
                        <ul class="layout-container screen">
                            <li data-layout="SCREEN_SMALL">
                                <div class="layout"></div>
                            </li>
                            <li data-layout="SCREEN_MIDDLE">
                                <div class="layout"></div>
                            </li>
                            <li data-layout="SCREEN_LARGE">
                                <div class="layout"></div>
                            </li>
                            <li data-layout="SCREEN_FULL">
                                <div class="layout"></div>
                            </li>
                        </ul>
                        <label class="sub-title fs-s bold">${i18next.t('scr_pos')}</label>
                        <ul class="layout-container position">
                            <li data-layout="TOP_LEFT">
                            <div class="layout"></div>
                            </li>
                            <li data-layout="TOP_RIGHT">
                            <div class="layout"></div>
                            </li>
                            <li data-layout="BOTTOM_LEFT">
                            <div class="layout"></div>
                            </li>
                            <li data-layout="BOTTOM_RIGHT">
                            <div class="layout"></div>
                            </li>
                        </ul>
                        <div class="btn-container">
                            <button class="btn-popup-cancel fs-s fc-white">${i18next.t('cancel')}</button>
                            <button class="btn-popup-ok fs-s fc-white">${i18next.t('join')}</button>
                        </div>
                        <button class="btn-popup-close"></button>
                    </div>
                </div>
            `);
        }
        
        _objID = objID;
        _onOkEvent = onOK;
        _onUpdateEvent = onUpdate;

        $popup_container = $('#popup-video-player');

        DragElement($popup_container.find('.popup').get(0));

        $video_player = $('#player-in-popup').get(0);
        $video_player.controlsList = "nodownload";
        
        clearEventHandler();
        bindEventHandler();

        // 스트림형식일때와 비디오 형식일떄 동작을 다르게, 자동재생해버리면 참여시 헷갈릴 수 있음
        if(src instanceof MediaStream) {
            $video_player.srcObject = src;
            $video_player.oncanplay = function(e) {
                $video_player.play()
            }
        } else {
            $video_player.src = src;
            $video_player.load();
            // $video_player.play();
            $video_player.crossOrigin = 'anonymous';
            $video_player.currentTime = _objID ? StreamMixer.getCurrentTime(_objID) : 0;
        }
        
        if(_objID) {
            $video_player.play()
        } else {
            $video_player.pause();
        }

        // $('#popup-video-player').find('#player-in-popup').attr('controls', false);
        setUI_SelectedItem(defaultPosition)

        POPUP.open($popup_container,
            null,
            () => {
                _objID = null;
                $video_player.pause();
            });
    }

    const setUI_SelectedItem = (pos) => {
        if(pos) {
            _selectedScreen = pos.screen;
            _selectedPosition = pos.location;
        } else {
            _selectedScreen = "SCREEN_SMALL";
            _selectedPosition = "TOP_LEFT";
        }
        $(`.layout-container.screen li`).removeClass('selected');
        $(`.layout-container.position li`).removeClass('selected');
        $(`.layout-container.screen li[data-layout="${_selectedScreen}"]`).addClass('selected');
        $(`.layout-container.position li[data-layout="${_selectedPosition}"]`).addClass('selected');
    }


    
    const clearEventHandler = () => {
        $popup_container.find('.layout-container.screen li').off();
        $popup_container.find('.layout-container.position li').off();
        $popup_container.find('.btn-popup-ok').off();
        $popup_container.find('.btn-popup-cancel, .btn-popup-close').off();
    }

    const bindEventHandler = () => {
        $video_player.onseeked = () => {
            if(_objID)
                StreamMixer.setCurrentTime(_objID, $video_player.currentTime)
        }

        $video_player.onvolumechange = () => {
            if(_objID) {
                StreamMixer.setVolume(_objID, $video_player.muted ? 0 : $video_player.volume)
            }
        }

        $video_player.onpause = () => {
            if(_objID)
                StreamMixer.pause(_objID);
        }

        $video_player.onplay = () => {
            if (_objID)
                StreamMixer.play(_objID);
        }

        $popup_container.find('.layout-container.screen li').click( (e) => {
            e.preventDefault();
            $('.layout-container.screen li').removeClass('selected');
            $(e.currentTarget).addClass('selected');
            _selectedScreen = $(e.currentTarget).attr('data-layout');
            if (_selectedScreen === "SCREEN_FULL") {
                $('.layout-container.position li').attr('disabled', true);
            } else {
                $('.layout-container.position li').attr('disabled', false);
            }
        })

        $popup_container.find('.layout-container.position li').click((e) => {
            e.preventDefault();
            if (_selectedScreen === "SCREEN_FULL") return;
            $('.layout-container.position li').removeClass('selected');
            $(e.currentTarget).addClass('selected');
            _selectedPosition = $(e.currentTarget).attr('data-layout');
        })


        $popup_container.find('.btn-popup-ok').click(e => {
            e.preventDefault();
            let convertedCode;
            if (_selectedScreen !== "SCREEN_FULL") {
                convertedCode = _selectedPosition + _selectedScreen.replace('SCREEN', '');
            } else {
                convertedCode = "FULL";
            }

            const values = PRESET_POSITION[convertedCode];

            if (_objID) {
                // objID를 가지고있으면 이미 재생중인 비디오, 그래서 update를 수행
                StreamMixer.setPosition({ id: _objID, ...values})
                if (_onUpdateEvent) {
                    _onUpdateEvent(
                        _objID,
                        values,
                        {
                            screen: _selectedScreen,
                            location: _selectedPosition
                        }
                    )
                }
            } else {
                // objID가 없으면 새로 
                if (_onOkEvent) {
                    _onOkEvent(
                        values,
                        {
                            screen: _selectedScreen,
                            location: _selectedPosition
                        },
                        $video_player.currentTime
                    );
                    _onOkEvent = null;
                }
            }
            POPUP.close();
            // $('#player-in-popup').get(0).pause();
        })
        // $video_player.on('contextmenu', function () { return false; });
        $popup_container.find('.btn-popup-cancel, .btn-popup-close').click( e => {
            e.preventDefault();
            $('#player-in-popup').get(0).pause();
        })
    }


    return {
        open
    }
})()