

export default (() => {
    const DIALOG_TYPE = {
        NONE: 0,
        OK: 1,
        OK_CANCEL: 2
    }

    let $ORIGIN_PARENT = null;
    let $POPUP_CONTAINER = null;
    let $POPUP = null;
    let _onOkHanlder = null;
    let _onCloseHandler = null;

    const open = (elPopupContainer, onOk, onClose) => {
        $POPUP_CONTAINER = elPopupContainer;
        _onOkHanlder = onOk;
        _onCloseHandler = onClose;
        $POPUP = $POPUP_CONTAINER.find('.popup');
        $ORIGIN_PARENT = $POPUP_CONTAINER.parent();

        $('#contents-container').after($POPUP_CONTAINER);

        $POPUP_CONTAINER.show();
        $POPUP.hide();
        $POPUP.get(0).style.top = '50%'; 
        $POPUP.get(0).style.left = '50%'; 
        setTimeout(e => {
            if($POPUP && $POPUP.fadeIn) {
                $POPUP.fadeIn(200);
            } else {
                $POPUP.show();
            }
        }, 10)
        bindEventHandler();
        stopWheel();
    };

    const close = () => {
        if ($POPUP_CONTAINER) {
            $POPUP_CONTAINER.appendTo($ORIGIN_PARENT);
            $POPUP_CONTAINER.hide();
            $POPUP = null;
            $POPUP_CONTAINER = null;
            $ORIGIN_PARENT = null;
            if (_onCloseHandler) {
                _onCloseHandler();
                _onCloseHandler = null;
            }
            startWheel();
        }
    };

    const bindEventHandler = () => {
        // 배경 클릭시 닫히는 기능 제거. 2019-05-17 전기만
        // $POPUP_CONTAINER.click(() => {
        //     close();
        // });
        $POPUP_CONTAINER.children('.popup').click((e) => {
            e.stopPropagation();
        });

        $(document).keyup((e) => {
            if (e.key.toLowerCase() === "escape") {
                close();
            }
        })

        if ($POPUP.find('.btn-popup-cancel')) {
            const $btn_cancel = $POPUP.find('.btn-popup-cancel');
            $btn_cancel.click( (e) => {
                if(_onCloseHandler) {
                    _onCloseHandler();
                    _onCloseHandler = null;
                }
                close();
            })
        }

        if ($POPUP.find('.btn-popup-close')) {
            const $btn_close = $POPUP.find('.btn-popup-close');
            $btn_close.click( (e) => {
                if (_onCloseHandler) {
                    _onCloseHandler();
                    _onCloseHandler = null;
                }
                close();
            })
        }
    }


    let $DIALOG_CONTAINER = null;
    let $DIALOG = null;
    let _okEvent = null;
    let _cancelEvent = null;

    const alert = (msg, okEvent) => {
        openDialog({ type: DIALOG_TYPE.OK, msg, okEvent, cancelEvent: okEvent });
    }
    const openDialog = ({
        type,
        msg,
        okMsg,
        cancelMsg,
        okEvent,
        cancelEvent
    }) => {
        _okEvent = okEvent;
        _cancelEvent = cancelEvent;


       
        $('.dialog-container').remove();
        

        if(type === DIALOG_TYPE.OK_CANCEL) {
            $(document.body).append(`
                <div class="dialog-container">
                    <div class="dialog no-select">
                        <div class="msg-container">
                            <span class="msg">${msg}</span>
                        </div>
                        <div class="btn-container">
                            <button id="btn-dialog-cancel">${cancelMsg ? cancelMsg : i18next.t("cancel")}</button>
                            <button id="btn-dialog-ok">${okMsg ? okMsg : i18next.t("ok")}</button>
                        </div>
                        <button id="btn-dialog-close"></button>
                    </div>
                </div>
            `)
        } else {
            $(document.body).append(`
                <div class="dialog-container">
                    <div class = "dialog no-select">
                        <div class="msg-container">
                            <span class="msg">${msg}</span>
                        </div>
                        <div class="btn-container">
                            <button id="btn-dialog-ok">${okMsg ? okMsg : i18next.t('ok')}</button>
                        </div>
                        <button id="btn-dialog-close"></button>
                    </div>
                </div>
            `)
        }


        $DIALOG_CONTAINER = $('.dialog-container');
        $DIALOG = $DIALOG_CONTAINER.find('.dialog');
        
        $DIALOG_CONTAINER.show();
        $DIALOG.hide();
        setTimeout(e => {
            if($DIALOG && $DIALOG.fadeIn) {
                $DIALOG.fadeIn(200);
            } else {
                $DIALOG.show();
            }
        }, 20)
        
        bindDialogEventHandler();
        stopWheel();

        if ($POPUP_CONTAINER) {
            $POPUP_CONTAINER.hide();
        }

    }

    const closeDialog = () => {
        if($DIALOG_CONTAINER) {
            $DIALOG_CONTAINER.remove();
            $DIALOG = null;
            $DIALOG_CONTAINER = null;
        }
        if ($POPUP_CONTAINER) {
            $POPUP_CONTAINER.show();
        }
        startWheel();
    }

    const bindDialogEventHandler = () => {
        $DIALOG_CONTAINER.find('#btn-dialog-close').click((e) => {
            e.preventDefault();
            closeDialog();
            if (_cancelEvent) { _cancelEvent() }
        })

        $DIALOG_CONTAINER.find('#btn-dialog-cancel').click((e) => {
            e.preventDefault();
            closeDialog();
            if(_cancelEvent) { _cancelEvent() }
        })

        $DIALOG_CONTAINER.find('#btn-dialog-ok').click((e) => {
            e.preventDefault();
            closeDialog();
            if(_okEvent) {
                _okEvent()
            }
        })
    }

    const stopWheel = () => {
        $('body').css('overflow', 'hidden');
    }

    const startWheel = () => {
        $('body').css('overflow', '');
    }

    return {
        open, close, DIALOG_TYPE, openDialog, alert
    }
})()

