const StartLiveButton = ( () => {
    let $btn_live_start =  null;
    let $btn_live_end = null;
    let _enabled = false;
  
    const init = () => {
        $btn_live_start = $('#btn-live-start');
        $btn_live_end = $('#btn-live-end');

        setStartStopMode("start");
        setEnable(false);
    };

    const attachEventHandler = ({startEvent, endEvent}) => {
        $btn_live_start.click( () => {
            if (!_enabled) {
                alert($.i18n.t('wrng_err_n_try'));
                return;
            }
            startEvent();
        });

        $btn_live_end.click( () => {
            confirm($.i18n.t('end_cast'),
            () => {
                endEvent();
            })
        });
    };

    const setEnable = (enable) => {
        _enabled = enable;
    };

    const setStartStopMode = (mode) => {
        if (mode === 'start' || mode === true) {
            $btn_live_start.show();
            $btn_live_end.hide();
        } else {
            $btn_live_start.hide();
            $btn_live_end.show();
        }
    };

    return {
        init,
        setStartStopMode,
        attachEventHandler,
        setEnable
    }

    
})()

export default StartLiveButton