import P2PController from './live.p2p_controller.v2';
import LiveService, { LIVE_STATE } from '../../../services/live.service.js';

import { LIVE_STATUS, LIVE_INFO, SERVICE_ID, RELAY } from '../store/live.store';
// import JWTParser from '../../../utils/JWTParser';
import {
  listen,
  produce,
  LIVE as LIVE_EVENT,
  MIX as MIX_EVENT,
} from '../../../event';

import { SCREEN, PRESET_POSITION } from '../POSITIONS';
import { Stream } from 'stream';

export default (() => {
  let _broadcaster = null;
  let _artc = null;
  let _joinUserSeq = null;
  let _layoutCode = null;

  const init = () => {
    _initCustomEvent();
  };

  const startLive = async ({
    title,
    desc,
    onOpenHandler,
    msgFilters = [],
    onCloseHandler,
    onErrorHandler
  }) => {
    const createLiveResponse = await LiveService.createLive();
    const {
      data: { cojam_service_session_id, live_seq, room_token }
    } = createLiveResponse;
    LIVE_INFO.set({ id: live_seq, room_token });
    SERVICE_ID.setMediaServiceID_LIVE(cojam_service_session_id);

    _broadcaster = new ARTC.LIVE.PUB();
    // console.log(StreamMixer.getAudioTracks());
    _broadcaster.start({
      // stream: StreamMixer.getMixedStream(),
      tracks: StreamMixer.getTracks(),
      token: room_token,
      userId: cojam_service_session_id,
      onOpen: async ( {rtcConnection} ) => {
        LIVE_STATUS.set(true);
        // API서버에 상태 전달
        await LiveService.setLiveStatus(
          LIVE_STATE.START,
          LIVE_INFO.get().live_seq,
          room_token
        );
        // p2p통신위한 소켓 오픈
        P2PController.start();

        // 멀티방송
        startRelay();
        if (onOpenHandler) {
          onOpenHandler(rtcConnection);
        }
      },
      onMessage: message => {
        msgFilters.map(msgFilter => {
          msgFilter(message);
        });
      },
      onClose: () => {
        console.log('** Live closed !!');
        onCloseHandler(),
        LIVE_STATUS.set(false);
      },
      onError: error => {
        console.log('** Live error:', error);

        LIVE_STATUS.set(false);
      }
    });
  };

  const endLive = async () => {
    try {
      if (_broadcaster === null) return;

      if (!RELAY.get() > 0) {
        _broadcaster.getSignal().stopRelay();
      }

      _broadcaster.getSignal().closeTx();
      _broadcaster.stop();
      _broadcaster = null;

      await LiveService.setLiveStatus(LIVE_STATE.END, LIVE_INFO.get().live_seq);

      LIVE_STATUS.set(false);
      // $('#close-modal-salsecnt').text($sales_cnt.text());
      // $('#close-modal-likecnt').text($like_cnt.text());
      // $('#shop_host_modal').show();
    } catch (err) {
      console.error(err);
    }
  };

  const invite = async userSession => {
    const token = P2PController.getToken();
    _broadcaster.sendOpMessage(
      JSON.stringify({
        cmd: 'invite',
        payload: { token }
      }),
      userSession
    ).then(res => {
      console.log('invite success:');
    }).catch(err => {
      console.error('invite error:', err);
    });
  };

  const cancelJoin = () => {
    P2PController.close();
  };

  const sendChatMessage = (avatar, nickname, message) => {
    // if (_broadcaster === null) {
    //   alert('연결되지 않았습니다.');
    //   return;
    // }
    _broadcaster.sendChatMessage(
      JSON.stringify({
        nickname,
        avatar,
        message
      })
    );
  };

  const startRelay = () => {
    if (RELAY.checkEmpty()) return;
    // send sns relay
    const SNS_RELAY_TEMP =
      RELAY.get().map(row => {
        if (row && row.data && row.data.url) {
          return {
            vendor: row.data.vendor,
            protocol: 'rtmp',
            url: row.data.url.endsWith('/') ? row.data.url : row.data.url + '/',
            stream_key: row.data.stream_key
          };
        } else {
          return {
            vendor: row.data.vendor,
            protocol: 'rtmp',
            id: row.data.id,
            pass: row.data.pass
          };
        }
      }) || [];

    if (SNS_RELAY_TEMP.length >= 1) {
      _broadcaster.getSignal().startRelay(SNS_RELAY_TEMP);
    }

    // send status
    // TODO: janus session ID required?
    // BROADCASTER.getSignal().status();
  };

  const _initCustomEvent = () => {
    listen(LIVE_EVENT.JOIN_VIDEO, async data => {
      const { itemIndex, url } = data;

    });

    // listen(MIX_EVENT.VIDEO, stream => {
    //   _mixer.mixStream({ stream });
    //   _mixer.videoMixer.setLayout(_layoutCode);
    // });
  };

  return {
    init,
    startLive,
    endLive,
    invite,
    cancelJoin,
    sendChatMessage,
  };
})();
