import POPUP from '../../../common_ui/popup';

import { PRESET_POSITION } from '../POSITIONS';

export default (() => {
    let $layout_select_popup = null;
    let _selectedScreen = "SCREEN_SMALL";
    let _selectedLocation = "TOP_LEFT";
    let _changeEvent = null;

    const init = (changeEvent) => {
        $layout_select_popup = $('#layout-select-popup');
        $layout_select_popup.hide();
        _changeEvent = changeEvent;
        selectScreen('SCREEN_FULL');
        selectLocation(_selectedLocation)
        bindEventHandler();
    }

    const selectScreen = (layoutCode) => {
        _selectedScreen = layoutCode;
        $layout_select_popup.find(`.layout-container.screen li`).removeClass('selected');
        $layout_select_popup.find(`.layout-container.screen li[data-layout=${layoutCode}]`).addClass('selected')
        if (_selectedScreen === "SCREEN_FULL") {
            $('.layout-container.position li').attr('disabled', true);
        } else {
            $('.layout-container.position li').attr('disabled', false);
        }
    }

    const selectLocation = (layoutCode) => {
        _selectedLocation = layoutCode;
        $layout_select_popup.find(`.layout-container.position li`).removeClass('selected');
        $layout_select_popup.find(`.layout-container.position li[data-layout=${layoutCode}]`).addClass('selected')
    }

    const open = () => {
        POPUP.open($layout_select_popup)
    }

    const bindEventHandler = () => {
        $layout_select_popup.find('.layout-container.screen li').click( e => {
            e.preventDefault();
            selectScreen($(e.currentTarget).attr('data-layout'))
        })

        $layout_select_popup.find('.layout-container.position li').click(e => {
            e.preventDefault();
            selectLocation($(e.currentTarget).attr('data-layout'))
        })

        $layout_select_popup.find('.btn-popup-ok').click( e => {
            e.preventDefault();
            let convertedCode;
            if (_selectedScreen !== "SCREEN_FULL") {
                convertedCode = _selectedLocation + _selectedScreen.replace('SCREEN', '');
            } else {
                convertedCode = "FULL";
            }

            const values = PRESET_POSITION[convertedCode];

            
            _changeEvent({
                position: values,
                position_code: {
                    screen: _selectedScreen,
                    location: _selectedLocation
                }
            });
            POPUP.close();
        })
    }

    return {
        init, open
    }
})()