import Chat from '../../common_ui/chat';

// controller
import { UserMedia, LiveControllerV2 as LiveController } from './controller';

// Services`
import LiveService from '../../services/live.service';

// store
import { LIVE_INFO, CHANNEL_INFO } from './store/live.store';

// constants
import { SCREEN, PRESET_POSITION } from './POSITIONS';

let _mixer = null;
let uiChatPanel = null;
let camStream = null;
let camID = null;
let uiCameraList = null;
let uiAudioList = null;
let _onair = false;

$(document).ready(async () => {
    if (!USER_SESSION) {
        location.href = "/login"
    }

    StreamMixer.init('local-main-video');
    // _mixer.start();
    // _mixer.setCssSize(420, 235);

    uiStartLiveButton.init();
    uiStartLiveButton.attachEventHandler({
        startEvent: startLive,
        endEvent: endLive
    });

    uiChannelInfoPanel.init();
    uiChannelSettingsPopup.init();
    uiRelayPopup.init();

    uiLayoutPopup.init(changeLayoutEvent);
    uiJoinScreen.init();
    uiJoinLiveListPanelV2.init();
    uiJoinVideoListPanel.init();

    uiStatsMonitor.init();

    uiCameraList = new DeviceList($('#btn-live-cam'), $('#context-menu-select-cam'));

    camStream = await UserMedia.init();
    const { objID, err } = await StreamMixer.addMedia({
        source: camStream,
        sourceType: 'stream',
        video: {
            zIndex: 1,
            ...PRESET_POSITION.FULL,
        },
        audio: {
            mediaType: 'mycam'
        }
    });
    camID = objID;

    uiCameraList.addItem(
        `<li id="0">카메라 <span class="state fc-light-green">On</span></li>`,
        function(e) {
            if (StreamMixer) {
                videoState = !videoState;
                StreamMixer.enableCanvas(camID, videoState)
                const $state = $('#btn-live-cam').find('.state');
                const $stateInMenu = $('#context-menu-select-cam').find('.state')
                if (videoState) {
                    $state.text('On')
                    $state.removeClass('fc-gray');
                    $state.addClass('fc-light-green');
                    $stateInMenu.text('On');
                    $stateInMenu.removeClass('fc-gray');
                    $stateInMenu.addClass('fc-light-green');
                } else {
                    $state.text('Off');
                    $state.removeClass('fc-light-green');
                    $state.addClass('fc-gray');
                    $stateInMenu.text('Off');
                    $stateInMenu.removeClass('fc-light-green');
                    $stateInMenu.addClass('fc-gray')
                }
            }
        }
    );

    // uiCameraList.init(UserMedia.getDeviceList());

    UserMedia.getDeviceList().video.map( (video, index) => {
        const name = video.name.split(/ \(.*\)/gi)[0];
        uiCameraList.addItem(
            `<li data-device-id="${video.deviceId}"><span>${name}</span></li>`,
            async () => {
                // 카메라 전환시 지연이 발생하여 track을 stop시켜주지 않은 문제로 알았으나,
                // 이 부분과는 상관없는것으로 판단됨 (2019-11-20 기만)
                // if(camStream) {
                    // camStream.getTracks().forEach( track => track.stop() );
                // }
                const vID = video.deviceId;
                let aID = null;
                // 같은 장치(그룹)의 마이크가 있으면 묶어서, 없으면 오디오장치의 0번을 자동으로 선택
                let groupAudio = UserMedia.findGroupAudio(video.groupId);
                if(groupAudio) {
                    aID = groupAudio.deviceId;
                } else {
                    aID = UserMedia.getDeviceList().audio[0].deviceId;
                }
                const newStream = await UserMedia.getWebcamStream(vID, aID);
                camStream = newStream;

                // await StreamMixer.changeStream(camID, camStream)
                StreamMixer.removeMedia(camID)
                const { objID, err } = await StreamMixer.addMedia({
                    source: camStream,
                    sourceType: 'stream',
                    video: {
                        zIndex: 0,
                        ...PRESET_POSITION.FULL,
                    },
                    audio: {
                        mediaType: 'mycam'
                    }
                });
                camID = objID;
            }
        )
    })
    uiCameraList.selectItem(UserMedia.getCurrentDevice().video.deviceId)

    uiCameraList.setEnable(true);
    uiStartLiveButton.setEnable(true);

    uiShareMenu.init();
    uiShareMenu.setEnable(false, i18next.t('wrng_not_share'));

    uiChatPanel = new Chat();
    const chatSender = {
        avatar: USER_SESSION.avatar_url,
        name: USER_SESSION.nick_name
    };
    uiChatPanel.init('.chat-container', sendChatMessage, chatSender);

    LiveController.init();

    bindEventHandler();
    // 채널설정 초기화
    // 채널 정보 받아온 후 적용
    const resGetChannelInfo = await LiveService.getChannelInfo(USER_SESSION.user_seq)
    CHANNEL_INFO.set(resGetChannelInfo.data.channel_info);
    $('#spinner-init-rtc').hide();
    LIVE_INFO.setTitle(CHANNEL_INFO.get().title);
    LIVE_INFO.setDesc(CHANNEL_INFO.get().desc);

    uiChannelSettingsPopup.initData();
    uiJoinLiveListPanelV2.initData();
    uiJoinVideoListPanel.initData();
    uiJoinVideoListPanel.startJoinList();
});

const startLive = () => {
    // if (!await checkNotSettingRelay()) return;
    confirm(
        i18next.t("wrng_multistream_bf_start"),
        async () => {
            // const ctx = StreamMIxer.audioMixer.context;
            // await ctx.resume();
            // console.log('audio context resumed;');

            if (!CHANNEL_INFO.get().channel_seq === 0) {
                alert("채널 정보를 불러올 수 없습니다<br>다시 시도해 주세요");
                return;
            }

            LiveController.startLive({
                title: LIVE_INFO.get().title,
                desc: LIVE_INFO.get().desc,
                onOpenHandler: openLive,
                msgFilters: [recvChatMessage],
                onCloseHandler: closeLive
            });
            _onair = true;
            startLocalRecord();
        }
    );

    // 참여 목록 Refresh
    // intval_RefreshJoinList = setInterval(e => {
    //   handleGetJoinList(e)
    // }, 3000);


    // 라이브 종료시
    // $(window).bind('beforeunload', function (eventObject) {
    //   // var returnValue = undefined;
    //   // if (!inFormOrLink) {
    //   //   returnValue = "Do you really want to close?";
    //   // }
    //   // eventObject.returnValue = returnValue;
    //   // return returnValue;
    //   LiveController.endLive();
    // }); 
};
let mediaRecorder = null;
let recordedBlobs = [];
const startLocalRecord = () => {
    mediaRecorder = new MediaRecorder(camStream);
    recordedBlobs = [];
    mediaRecorder.ondataavailable = pushLocalRecordData;
    mediaRecorder.start(10);
}

const stopLocalRecord = () => {
    mediaRecorder.stop();
}

const pushLocalRecordData = (event) => {
    if (event.data && event.data.size > 0) {
        recordedBlobs.push(event.data);
    }
}

const endLive = () => {
    LiveController.endLive();
    uiChannelInfoPanel.stopViewCount();
    uiStartLiveButton.setStartStopMode('start');

    uiJoinLiveListPanelV2.stopJoinList();
    uiJoinLiveListPanelV2.clear();
    uiJoinVideoListPanel.stopJoinList();

    uiShareMenu.setEnable(false, i18next.t('wrng_not_share'))
    uiChannelSettingsPopup.setEnableAll(true);
    _onair = false;
    stopLocalRecord();
};

const openLive = (rtcConnection) => {
    uiStartLiveButton.setStartStopMode('stop');
    uiChannelInfoPanel.startViewCount();
    uiJoinLiveListPanelV2.startJoinList();
    uiChannelSettingsPopup.sendSettings();
    uiChannelSettingsPopup.setEnableAll(false);
    uiShareMenu.setData(`/live/${LIVE_INFO.get().live_seq}`, CHANNEL_INFO.get().title);
    uiShareMenu.setEnable(true);
    uiStatsMonitor.setRtcConnection(rtcConnection);
};

const closeLive = () => {
    uiChannelInfoPanel.stopViewCount();
    uiStartLiveButton.setStartStopMode('start');

    uiJoinLiveListPanelV2.stopJoinList();
    uiJoinLiveListPanelV2.clear();
    uiJoinVideoListPanel.stopJoinList();

    uiShareMenu.setEnable(false, i18next.t('wrng_not_share'))
}

const sendChatMessage = message => {
    var avatarUrl = USER_SESSION.avatar_url;
    var name = USER_SESSION.nick_name;
    LiveController.sendChatMessage(avatarUrl, name, message);
};

const recvChatMessage = recvMessage => {
    const parsedMessage = JSON.parse(recvMessage.msg);
    const { message, avatar, nickname } = parsedMessage;
    uiChatPanel.appendMessage(avatar, nickname, message);
};

