const changeLayoutEvent = (props) => {
    const { position, position_code } = props;
    StreamMixer.setPosition({
        id: camID,
        ...position,
        zIndex: position_code.screen === "SCREEN_FULL" ? 1 : 3
    })
}

let videoState = true, audioState = true;

const bindEventHandler = () => {
    $('#btn-live-mic').click((e) => {
        if (StreamMixer) {
            audioState = !audioState;
            StreamMixer.mute(camID, !audioState);
            if (audioState) {
                $(e.currentTarget).find('.state').text('On')
                $(e.currentTarget).find('.state').removeClass('fc-gray');
                $(e.currentTarget).find('.state').addClass('fc-light-green')
            } else {
                $(e.currentTarget).find('.state').text('Off');
                $(e.currentTarget).find('.state').removeClass('fc-light-green');
                $(e.currentTarget).find('.state').addClass('fc-gray')

            }
        }
    });
    // $('#btn-live-cam').click((e) => {
    //     if (StreamMixer) {
    //         videoState = !videoState;
    //         StreamMixer.enableCanvas(camID, videoState)
    //         if (videoState) {
    //             $(e.currentTarget).find('.state').text('On')
    //             $(e.currentTarget).find('.state').removeClass('fc-gray');
    //             $(e.currentTarget).find('.state').addClass('fc-light-green')

    //         } else {
    //             $(e.currentTarget).find('.state').text('Off');
    //             $(e.currentTarget).find('.state').removeClass('fc-light-green');
    //             $(e.currentTarget).find('.state').addClass('fc-gray')
    //         }
    //     }
    // });
    $('#btn-live-layout').click((e) => {
        uiLayoutPopup.open();
    });
    $('#btn-live-status').click(function (e) {
        uiStatsMonitor.open();
    })

    $('#ic-multistream-info').mouseenter((e) => {
        $('#popup-multistream-info').fadeIn(150);
    })
    $('#ic-multistream-info').mouseleave((e) => {
        $('#popup-multistream-info').hide();
    })


    // stream mixer의 draw 모드를 바꿉니다. (30fps -> requestAnimationFrame)
    // $('#switch-draw-mode').change( e => {
    //     e.preventDefault();
        
    //     const val = $('#switch-draw-mode').prop('checked');

    //     StreamMixer.setDrawMode(val)

    // })


    $('#btn-download-record').click( (e) => {
        if (recordedBlobs.length === 0) {
            alert("녹화된 영상이 없습니다");
            return;
        }
        if (_onair) {
            alert("방송 종료 후에 저장할 수 있습니다");
            return;
        }
        
        const blob = new Blob(recordedBlobs, { type: 'video/webm' });
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.style.display = 'none';
        a.href = url;
        a.download = 'test.webm';
        document.body.appendChild(a);
        a.click();
        setTimeout(() => {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);
        }, 100);
    })
}