// service
import LiveService from '../../../services/live.service';

// controller
import { LiveControllerV2 as LiveController } from '../controller';

// store
import { LIVE_INFO, JOIN_INFO, CHANNEL_INFO } from '../store/live.store';
const REFRESH_TIME = 3000;

// const
import { PRESET_POSITION } from '../POSITIONS';

// event
import {
	produce,
	listen,
	P2P as P2P_EVENT,
	MIX as MIX_EVENT
} from '../../../event';

let $list_panel = null;
let $contents = null;
let $btn_clear = null;
let $toggle_ignore = null;
let $list = null;
let $list_parent = null;
let $list_count = null;
let $loading_anim = null;

let intval_JoinList = null;
export default (() => {
	const init = () => {
		$list_panel = $('#panel-join-live');

		$contents = $list_panel.find('.contents');

		$btn_clear = $list_panel.find('#btn-clear-join-live');
		$toggle_ignore = $('#switch-join-live');

		$list = $('#join-live-list');
		$list_parent = $list.parent();
		$list_count = $list_panel.find('.count-container').find('.count');

		$loading_anim = $('#panel-join-video .contents .loading-anim');
		$loading_anim.hide();

		$list.addClass('empty');

		bindEventHandler();
	};

	const bindEventHandler = () => {
		$btn_clear.click(e => {
			e.preventDefault();

			clear();
		});

		$toggle_ignore.change(e => {
			e.preventDefault();
			const val = $toggle_ignore.prop('checked') ? 'Y' : 'N';
			LiveService.setIgnoreJoin_Live(CHANNEL_INFO.get().channel_seq, val);
			checkDisableContents();
		});
	};

	const initData = () => {
		$toggle_ignore.prop(
			'checked',
			CHANNEL_INFO.get().live_join_yn === 'Y' ? true : false
		);
		checkDisableContents();
	};

	const checkDisableContents = () => {
		if ($toggle_ignore.prop('checked')) {
			$contents.attr('disabled', false);
			$contents.find('*').attr('disabled', false);
		} else {
			$contents.attr('disabled', true);
			$contents.find('*').attr('disabled', true);
		}
	};

	const arrayDiff = (savedArray, currentArray) => {
		const intersect = savedArray.filter(
			savedUser =>
				!!currentArray.find(
					currentUser =>
						savedUser['user_seq'] === currentUser['user_seq']
				)
		);
		const deleted = savedArray.filter(
			savedUser =>
				!intersect.find(
					intersectUser =>
						savedUser['user_seq'] === intersectUser['user_seq']
				)
		);
		const added = currentArray.filter(
			currentUser =>
				!intersect.find(
					intersectUser =>
						currentUser['user_seq'] === intersectUser['user_seq']
				)
		);
		return {
			intersect,
			added,
			deleted
		};
	};

	let joinedUser = null; // 연결중인 p2pUserId state로 관리?
	let joinedStream = null;
	let beforeJoinList = [];

	const getJoinList = async event => {
		if (!$toggle_ignore.prop('checked')) return;
		try {
			const resJoinList = await LiveService.getJoinList(
				LIVE_INFO.get().live_seq
			);
			if (
				!resJoinList ||
				resJoinList.status === 400 ||
				!resJoinList.data
			) {
				return;
			}

			const joins = resJoinList.data.joins;

			$list.off('click', '.card.li-join-user button.btn-connect');
			$list.off('click', '.card.li-join-user button.btn-join');
			$list.off('click', '.card.li-join-user button.btn-close');

			if (!joins || joins.length === 0) {
				$list.empty();
				if (!$list.hasClass('empty')) {
					$list.addClass('empty');
				}
				$list_count.text(0);
				beforeJoinList = [];
			} else {
				const { added, deleted } = arrayDiff(beforeJoinList, joins);
				beforeJoinList = joins;

				$list.removeClass('empty');
				// $list.off('click');
				// $list.empty();

				$list = $list.detach();
				if (added.length > 0) {
					added.map(user => {
						const {
							session_key: sessionKey,
							user_seq: userSeq,
							thumbnail,
							avatar_url: avatarUrl,
							nick_name: nickname
						} = user;

						$list.append(`
							<li class="card li-join-user"
								data-join-user="${sessionKey}"
								data-user-seq="${userSeq}">
								${thumbnail ? `
									<div class="thumbnail-wrapper img">
										<img src="${thumbnail}">
									</div>`
									:
									`<div class="thumbnail-wrapper img _blank">
										<span class="fs-xs fc-white">CoJam.TV</span>
									</div>`
								}
								<div class="thumbnail-wrapper vid" style="display:none;">
									<video style="height:100%; max-height: 100%; display:none;" autoplay controls playsinline></video>
								</div>
								<div class="name-container">
									<div class="avatar-section">
										<img src="${avatarUrl}">
									</div>
									<div class="name-section fs-xs">
										<span class="name">${nickname}</span>
									</div>
								</div>
              					<div class="button-container connect" style="display:block;">
									<button class="btn-connect btn rnd small"
                  						${$toggle_ignore.prop('checked') ? '' : 'disabled=disabled'}>${i18next.t('conn')}</button>
              					</div>
								<div class="button-container connected" style="display:none;">
									<button class="btn-join btn rnd small">${i18next.t('btn_join')}</button>
									<button class="btn-close btn rnd small">${i18next.t('btn_close')}</button>
								</div>
							</li>
						`);
					});
				}

				// if (deleted.length > 0) {
				// 	console.log(deleted)
				// 	const userIds = deleted.map(user => user['user_seq']);
				// 	userIds.forEach(userId => {
				// 		const li = $list.find(`[data-user-seq='${userId}']`);
				// 		const objID = li.attr('data-obj-id');
				// 		StreamMixer.remove(objID);
				// 		li.off('click').remove();
				// 	});
				// }

				$list.appendTo($list_parent);

				if (JOIN_INFO.getVideoSeq()) {
					const joinButton = $list.find(
						`li[data-video-seq=${JOIN_INFO.getVideoSeq()}] .btn-join`
					);
					joinButton.text('참여');
					joinButton.removeClass('tr');
					joinButton.removeClass('joined');
				}

				// resJoinList.data.cnt에서 전체 목록수를 받아와야하는데
				// 데이터가 넘어오지 않고있음. 수정 필요
				// 임의로 length로 처리
				$list_count.text(joins.length);
			}

			$list.on('click', '.card.li-join-user button.btn-connect', event =>
				handleConnectBtnClick(event)
			);
			$list.on('click', '.card.li-join-user button.btn-join', event =>
				handleJoinBtnClick(event)
			);
			$list.on('click', '.card.li-join-user button.btn-close', event =>
				handleCloseBtnClick(event)
			);
		} catch (err) {
			console.error(err);
		}
	};

	const startJoinList = () => {
		intval_JoinList = setInterval(() => {
			getJoinList();
		}, REFRESH_TIME);
	};

	const stopJoinList = () => {
		clearInterval(intval_JoinList);
	};

	const clear = () => {
		$list.find('li').map( (idx, item) => {
			const objID = $(item).attr('data-obj-id');
			StreamMixer.removeMedia(objID);
		})

		LiveService.clearJoinList_Live(
			LIVE_INFO.get().live_seq,
			CHANNEL_INFO.get().channel_seq
		);
		
		$list_count.text(0);
		$list.empty();
		$list.addClass('empty');
		stopJoinList();
	};

	const handleConnectBtnClick = e => {
		e.preventDefault();

		const $this = $(e.target);
		const rootElement = $this.parent().parent();
		const userSession = rootElement.attr('data-join-user');
		const userSeq = rootElement.attr('data-user-seq');
		joinedUser = userSeq;

		JOIN_INFO.setUserSeq(userSeq);

		LiveController.invite(userSession);

		$this.parent().hide();
		rootElement.find('.connected').show();
	};

	const handleJoinBtnClick = async (e) => {
		e.preventDefault();
		const rootEl = $(e.target).closest('.li-join-user');
		if (!$(e.currentTarget).hasClass('joined')) {
			StreamMixer.addMedia(
				{
					source: joinedStream,
					sourceType: 'stream',
					video: {
						...PRESET_POSITION.BOTTOM_RIGHT_SMALL,
						autoSize: true,
						zIndex: 1,
						removeAfterEnd: true,
					},
					audio: {
						mediaType: 'mic',
						muted: false
					}
			}).then( objID => {
				// produce(MIX_EVENT.VIDEO, joinedStream);
				rootEl.attr('data-obj-id', objID.objID);

				const $btn = $(e.currentTarget);
				$btn.addClass('tr');
				$btn.addClass('joined');
			})

		} else {
			$(e.currentTarget).removeClass('tr');
			$(e.currentTarget).removeClass('joined');
			const objID = rootEl.attr('data-obj-id');
			StreamMixer.removeMedia(objID);

		}

	};

	const handleCloseBtnClick = e => {
		e.preventDefault();

		const $this = $(e.currentTarget);

		LiveController.cancelJoin();

		const $parent = $this.parent();
		const $root = $parent.parent();
		$parent.hide();
		$root.find('.connect').show();
		$root.find('.thumbnail-wrapper.vid').hide();
		$root.find('.thumbnail-wrapper.vid > video').hide();
		$root.find('.thumbnail-wrapper.img').show();
		$root.find('.btn-join').removeClass('tr');
		$this.removeClass('tr');
		$this.removeClass('joined');

		const objID = $root.attr('data-obj-id')
		StreamMixer.removeMedia(objID)
	};

	const remove = () => {
		
	}

	const removeAll = () => {
		const $li = $list.find('li');
		$li.map( item => {
			handleCloseBtnClick(item)
		})
	}

	const initCustomEvent = () => {
		listen(P2P_EVENT.ONTRACK, ({ streams, track }) => {
			if (joinedUser === null) return;

			const $elem = $list.find(`[data-user-seq='${joinedUser}']`);
			$elem.find('.thumbnail-wrapper.img').hide();
			$elem.find('.thumbnail-wrapper.vid').show();
			$elem.find('.thumbnail-wrapper.vid > video').show();
			$elem
				.find('.thumbnail-wrapper.vid')
				.find('video')
				.get(0).srcObject = streams[0];
			joinedStream = streams[0];
		});
		listen(P2P_EVENT.ONCLOSE, () => {
			if (joinedUser === null) return;

			const $elem = $list.find(`[data-user-seq='${joinedUser}']`);
			if ($elem) {
				$elem.find('.thumbnail-wrapper.vid')
					.find('video')
					.get(0).srcObject = null;
				$elem.find('.thumbnail-wrapper.vid').hide();
				$elem.find('.thumbnail-wrapper.vid > video').hide();
				$elem.find('.thumbnail-wrapper.img').show();

				const objID = $elem.attr('data-obj-id');
				StreamMixer.removeMedia(objID);
			}

		});
	};
	initCustomEvent();

	return {
		init,
		initData,
		startJoinList,
		stopJoinList,
		clear
	};
})();
