import { LIVE_INFO, SERVICE_ID, JOIN_INFO } from '../store/live.store';

import LiveService from '../../../services/live.service';
import {
    produce,
    P2P as P2P_EVENT
} from '../../../event';

class P2PControllerV2 {
    constructor() {
        this._caller = null;
        // this._mixer = null;
        this._init = false;
        this._opened = false;
        this._token = null;
    }

    async init() {
        return new Promise( (resolve, reject) => {
            this._caller = new ARTC.P2P.CALLER();
            // this._mixer = mixer;
    
            LiveService.createRoom_P2P()
            .then(resRoomInfo => {
                this._token = resRoomInfo.data.room_token;
        
                SERVICE_ID.setMediaServiceID_P2P(
                    resRoomInfo.data.cojam_service_session_id
                );
                this._init = true;

                return resolve();
            })
        })

    }

    getInitState() {
        return _init;
    };

    async start() {
        this.init()
        .then( () => {

            this._caller.start({
                // tracks: StreamMixer.getAudioTracks(),
                // tracks: StreamMixer.getTracks(),
                token: this._token,
                userId: SERVICE_ID.getMediaServiceID_P2P(),
                onOpen: () => {},
                onTrack: async event => {
                    const { streams, track } = event;
                    produce(P2P_EVENT.ONTRACK, { streams, track });
                },
                onEnter: token => {
                    this._token = token;
                    // produce(P2P_EVENT.ONENTER, token);
                },
                onMessage: event => produce(P2P_EVENT.ONMESSAGE, event),
                onClose: () => {
                    this._caller.closeRtc();
                    // _mixer.recoverOriginStream();
                    // _mixer.videoMixer.setLayout(layout);
                    JOIN_INFO.deleteUserSeq();

                    LiveService.cancelJoin(
                        LIVE_INFO.get().live_seq,
                        JOIN_INFO.getUserSeq()
                    ).then(() => {
                        $(event.target).remove();
                    });
                    produce(P2P_EVENT.ONCLOSE);
                },
                onError: err => {
                    console.error('** P2P caller error:', err);
                    produce(P2P_EVENT.ONERROR, err);
                },
                onOperation: event => {
                    const { cmd } = event;
                    if (cmd === 'close') {
                        this._caller.closeRtc();
                        // this._mixer.recoverOriginStream();
                        // this._mixer.videoMixer.setLayout(layout);
                        JOIN_INFO.deleteUserSeq();

                        LiveService.cancelJoin(
                            LIVE_INFO.get().live_seq,
                            JOIN_INFO.getUserSeq()
                        ).then(() => {
                            $(event.target).remove();
                        });
                        produce(P2P_EVENT.ONCLOSE);
                    }
                }
            });
        })
    };

    close() {
        if (this._caller) {
            this._caller.sendClose();
            this._caller.closeRtc();
        }
    };

    getState() {
        return this._opened;
    };

    getToken() {
        return this._token;
    };
}

const p2pController = new P2PControllerV2();
export default p2pController;