export default ({ url, type = 'GET', data, cache, contentType}) => {
    if (data) {
      if(contentType === 'text/plain') {
        return new Promise((resolve, reject) => {
          $.ajax({
            url: url,
            type,
            contentType: 'text/plain',
            data,
            cache: cache ? cache : true,
            success: function (res) {
              // console.log(res)
              return resolve(res);
            },
            error: function (err) {
              return reject(err);
            }
          })
        })        
      } else {
        return new Promise((resolve, reject) => {
          $.ajax({
            url: url,
            type,
            dataType: 'json',
            contentType: 'application/json',
            data,
            cache: cache ? cache : true,
            success: function (res) {
              // console.log(res)
              return resolve(res);
            },
            error: function (err) {
              return reject(err);
            }
          })
        })
      }
    } else {
      return new Promise((resolve, reject) => {
  
        $.ajax({
          url: url,
          type,
          cache: cache ? cache : true,
          success: function (res) {
            // console.log(res)
            return resolve(res);
          },
          error: function (err) {
            return reject(err);
          }
        })
      })
    }
  
  }

