import LiveService from '../../../services/live.service';
import Fetch from '../../../utils/Fetch';
import { CHANNEL_INFO, LIVE_INFO } from '../store/live.store';
const ChannelInfoPanel = ( () => {

    let $input_channel_title = $('#channel-title');
    let $btn_modify_channel_title = $('#btn-modify-channel-title');
    let $lbl_title_channel_setting = $('.title-channel-setting');
    let $layoutSelectPopup = $('#layout-select-popup');

    let $lbl_live_count = null;
    let $lbl_like_count = null;
    let $lbl_view_count = null;

    let intVal_RefreshChanneCountInfo;
    const init = () => {
        $lbl_title_channel_setting = $('.title-channel-setting');
        $input_channel_title = $('#channel-title');
        $btn_modify_channel_title = $('#btn-modify-channel-title');
        $layoutSelectPopup = $('#layout-select-popup');

        $lbl_live_count = $('#live-count span');
        $lbl_like_count = $('#btn-like span');
        $lbl_view_count = $('#view-count span');

    };

    const startViewCount = () => {
        getAndApplyCounts();
        // 부가 정보 받아오기 설정
        intVal_RefreshChanneCountInfo = setInterval(() => {
            getAndApplyCounts();
        }, 1000);

        function getAndApplyCounts() {
            LiveService.getChannelCountInfo(LIVE_INFO.get().live_seq)
            .then( res => {
                $lbl_live_count.text(res.data.live_cnt)
                $lbl_like_count.text(res.data.like_cnt)
                $lbl_view_count.text(res.data.view_cnt)
            });
        }
    };

    const stopViewCount = () => {
        clearInterval(intVal_RefreshChanneCountInfo);
    };

    return {
        init,
        startViewCount, stopViewCount
    }
})()

export default ChannelInfoPanel