import uiPopupVideoPlayer from '../../../common_ui/PopupVideoPlayer';
import { PRESET_POSITION} from '../POSITIONS';

// service
import LiveService from '../../../services/live.service';
import VideoService from '../../../services/video.service';

// controller
import { produce, listen, LIVE as LIVE_EVENT } from '../../../event';

// store
import { LIVE_INFO, JOIN_INFO, CHANNEL_INFO } from '../store/live.store';
const REFRESH_TIME = 3000;

let $list_panel = null;
let $contents = null;
let $btn_clear = null;
let $toggle_ignore = null;
let $list = null;
let $list_count = null;
let $loading_anim = null;

let intval_JoinList = null;
let oldList = [];

export default (() => {

    const init = () => {
        $list_panel = $('#panel-join-video');
        $contents = $list_panel.find('.contents');
        $btn_clear = $list_panel.find('#btn-clear-join-video');
        $toggle_ignore = $list_panel.find('#switch-join-video');

        $list = $('#join-video-list');
        $list_count = $list_panel.find('.count-container').find('.count');


        $loading_anim = $('#panel-join-video .contents .loading-anim');
        $loading_anim.hide();

        if ($.i18n && $.i18n.t) {
            $list.append(`<span class="empty-msg">${$.i18n.t('not_joined')}</span>`);
        } else {
            $list.append(`<span class="empty-msg" data-i18n="not_joined"></span>`);
        }
        bindEventHandler();
    };

    const bindEventHandler = () => {

        $btn_clear.click((e) => {
            e.preventDefault();
            LiveService.clearJoinList_Video(CHANNEL_INFO.get().channel_seq)
                .then(res => {
                    const { status, message } = res;
                    if (status === 200) {
                        clear();
                    } else {
                        alert($.i18n.t('wrng_err_n_try'))
                    }
                })
        })

        $toggle_ignore.change((e) => {
            e.preventDefault();
            const val = $toggle_ignore.prop('checked') ? "Y" : "N";
            LiveService.setIgnoreJoin_Video(CHANNEL_INFO.get().channel_seq, val);
            checkDisableContents();
        })

        // listen(LIVE_EVENT.STREAM_ADD_SUCCESS, data => {
        //     const { itemIndex, id } = data;
        //     Object.assign(oldList[itemIndex], { objid: id });
        // });

        // listen(LIVE_EVENT.JOIN_VIDEO_CANCEL, data => {
        //     const { itemEl } = data;
        //     oldList.findIndex( d => )
        // })
    }

    const initData = () => {
        $toggle_ignore.prop('checked', CHANNEL_INFO.get().vod_join_yn === "Y" ? true : false);
        checkDisableContents();
    }

    const checkDisableContents = () => {
        if ($toggle_ignore.prop('checked')) {
            $contents.attr('disabled', false)
            $contents.find('*').attr('disabled', false);
            $contents.removeClass('no-select');
        } else {
            $contents.attr('disabled', true)
            $contents.find('*').attr('disabled', true);
            $contents.addClass('no-select');
        }
    }

    const getJoinList = async event => {
        if (!$toggle_ignore.prop('checked')) return;
        try {
            const resJoinList = await LiveService.getJoinList_Video(
                CHANNEL_INFO.get().channel_seq,
                0,
                20
            );

            if (!resJoinList ||
                resJoinList.status === 400 ||
                !resJoinList.data) { return; }

            let joins = resJoinList.data.vod_joins;

            oldList = addList(joins, oldList)
            
            function addList(newList, oldList) {
                // 없어진 데이터 리스트에서 삭제
                oldList.map(oldData => {
                    if (newList.find(newData => oldData.channel_seq === newData.channel_seq && oldData.vod_Seq === newData.vod_seq)) {
                        const idx = oldList.findIndex(d => d.channel_seq === oldData.channel_seq && d.vod_seq === oldData.vod_seq);
                        oldList.splice(idx, 1);
                    }
                })
                // 추가된 데이터 리스트에 추가
                newList.map(newData => {
                    if (!oldList.find(oldData => oldData.channel_seq === newData.channel_seq && oldData.vod_seq === newData.vod_seq)) {
                        oldList.push(newData)
                    }
                })
                return oldList;
            }

            if (!joins || joins.length === 0) {
                $list.empty();
                $list.append(`<span class="empty-msg">${$.i18n.t('not_joined')}</span>`);
                $list_count.text(0);
            } else {
                $list.find('.empty-msg').remove();
                $list.off('click');
                $list.empty();
                oldList.map(video => {
                    $list.append(`
                        <li class="card li-join-video"
                            data-video-seq="${video.vod_seq}"
                            data-user-seq="${video.user_seq}"
                            ${video.objID ? `data-obj-id=${video.objID}` : ''}>

                            ${video.thumbnail_path ?
                                `<div class="thumbnail-wrapper">
                                    <img src="${video.thumbnail_path}">
                                </div>`
                                :
                                `<div class="thumbnail-wrapper _blank">
                                    <span class="fs-xs fc-white">CoJam.TV</span>
                                </div>`
                            }

                            <div class="title-container fs-xs">
                                ${video.title}
                            </div>

                            <div class="name-container">
                                ${video.profile_img_url && video.profile_img_url !== "" ?
                                `<div class="avatar-section">
                                    <img src="${video.profile_img_url}">
                                </div>`
                                :
                                `<div class="avatar-section">
                                    <img src="/static/cojam/res/ic-blank-avatar.png">
                                </div>`
                                }
                                <div class="name-section fs-xs">
                                    <span class="name">${video.nick_name}</span>
                                </div>
                            </div>
                            <div class="button-container">
                            ${video.objID ?
                                `<button class="btn-join btn rnd small tr joined"
                                    ${$toggle_ignore.prop('checked') ? "" : "disabled=disabled"}>
                                    ${i18next.t('cancel')}
                                </button>`
                                :
                                `<button class="btn-join btn rnd small"
                                    ${$toggle_ignore.prop('checked') ? "" : "disabled=disabled"}>
                                    ${i18next.t('join')}
                                </button>`
                            }
                            </div>
                        </li>
                    `);
                });

                // if (JOIN_INFO.getUserSeq()) {
                //     const joinButton = $list.find(
                //         `li[data-user-seq=${JOIN_INFO.getUserSeq()}] .btn-join`
                //     );
                //     joinButton.text(i18next.t('join'));
                //     joinButton.removeClass('tr');
                //     joinButton.removeClass('joined');
                // }

                // if (JOIN_INFO.getVideoSeq()) {
                //     const joinButton = $list.find(
                //         `li[data-video-seq=${JOIN_INFO.getVideoSeq()}] .btn-join`
                //     );
                //     joinButton.text(i18next.t('cancel'));
                //     joinButton.addClass('tr');
                //     joinButton.addClass('joined');
                // }

                $list_count.text(resJoinList.data.cnt)

                bindEventAllItems();

            }
        } catch (err) {
            console.error(err);
        }
    };

    const startJoinList = async () => {
        getJoinList();
        intval_JoinList = setInterval(() => {
            getJoinList();
        }, REFRESH_TIME);
    };

    const stopJoinList = () => {
        clearInterval(intval_JoinList);
    };

    const clear = () => {
        $list.empty();
        $list.addClass('empty');
        $list_count.text(0);
    };

    const bindEventAllItems = () => {
        $('.li-join-video .thumbnail-wrapper').click(async (e) => {
            e.preventDefault();
            if (!$toggle_ignore.prop('checked')) return;
            // const index = $('.li-join-video').index($(e.currentTarget).closest('.li-join-video'));
            // const vodSeq = oldList[index].vod_seq;
            const rootElement = $(e.currentTarget).closest('.li-join-video');
            const videoSeq = rootElement.attr('data-video-seq');
            const objID = rootElement.attr('data-obj-id');

            const resGetVideoInfo = await VideoService.getVideoInfo(videoSeq);
            const video = resGetVideoInfo.data.vod_path.find(k => k.vod_type === "MP4");
            const combinedPath = video.domain + video.path;

            const item = oldList.find(d => d.vod_seq === videoSeq );

            uiPopupVideoPlayer.open(
                combinedPath,
                objID,
                item ? item.position_code : null,
                (values, code, time) => {
                    addItem(videoSeq, values, code, time)
                },
                (objID, values, code) => {
                    updateItem(videoSeq, values, code)
                }
            )
        })

        $('.li-join-video button').click(async e => {
            e.preventDefault();
            const rootElement = $(e.currentTarget).closest('.li-join-video');

            if (!$(e.currentTarget).hasClass('joined')) {
                let videoSeq = rootElement.attr('data-video-seq');
                const listItem = oldList.find(k => k.vod_seq === videoSeq);
                let position = !listItem.position ? PRESET_POSITION.TOP_LEFT_SMALL : listItem.position;
                addItem(videoSeq, position)
            } else {
                const objID = rootElement.attr('data-obj-id');
                deleteItem(objID, rootElement)
            }
        });

        // $('.li-join-video .title-container')
    }

    const addItem = async (
        videoSeq,
        position = PRESET_POSITION.TOP_LEFT_SMALL,
        code = {
            screen: "SCREEN_SMALL",
            location: "TOP_LEFT"
        },
        time
    ) => {
        const resVideoInfo = await VideoService.getVideoInfo(videoSeq);
        const {
            data: { vod_path: videoInfo }
        } = resVideoInfo;

        const { domain, path } = videoInfo.find(vod => vod.vod_type === 'MP4');
        const vodURL = `${domain}${path}`;

        const { objID, err } = StreamMixer.addMedia({
            source: vodURL,
            sourceType: 'url',
            video: {
                ...position,
                zIndex: 1,
                removeAfterEnd: true,
                startTime: time
            },
            audio: {
                mediaType: 'media',
                muted: false
            },
            onended: function (objID) {
                deleteItem(objID)
            }
        })
        .then( ({objID}) => {
            Object.assign(
                oldList.find(k => k.vod_seq === videoSeq),
                { objID, position, position_code: code }
            );

            const rootEl = $(`.li-join-video[data-video-seq="${videoSeq}"]`);
            rootEl.attr('data-obj-id', objID);

            rootEl.find('button').text($.i18n.t('cancel'));
            rootEl.find('button').addClass('tr');
            rootEl.find('button').addClass('joined');
        })
        .catch( err => {
            console.log(err)
        })


    }

    const updateItem = (
        videoSeq,
        position = PRESET_POSITION.TOP_LEFT_SMALL,
        code = {
            screen: "SCREEN_SMALL",
            location: "TOP_LEFT"
        }
    ) => {
        let item = oldList.find(k => k.vod_seq === videoSeq);
        if(!item) return;
        item.position = position;
        item.position_code.screen = code.screen;
        item.position_code.location = code.location;
    }

    const deleteItem = (objID) => {
        if (oldList.find(k => k.objID === objID)) {
            delete oldList.find(k => k.objID === objID).objID
        }

        StreamMixer.removeMedia(objID)

        const rootEl = $(`.li-join-video[data-obj-id="${objID}"]`);
        rootEl.removeAttr('data-obj-id')

        rootEl.find('button').text($.i18n.t('join'));
        rootEl.find('button').removeClass('tr');
        rootEl.find('button').removeClass('joined');
    }


    return {
        init,
        initData,
        startJoinList,
        stopJoinList,
        clear
    };
})();

