import uiPopupVideoPlayer from '../../../common_ui/PopupVideoPlayer';
import { PRESET_POSITION } from '../POSITIONS';

// service
import LiveService from '../../../services/live.service';
import VideoService from '../../../services/video.service';

// controller
import { produce, listen, LIVE as LIVE_EVENT } from '../../../event';

// store
import { LIVE_INFO, JOIN_INFO, CHANNEL_INFO } from '../store/live.store';
const REFRESH_TIME = 3000;

let $list_panel = null;
let $contents = null;
let $btn_add = null;
// let $toggle_ignore = null;
let $list = null;
let $list_count = null;
let $loading_anim = null;

let _intVal_checkStream = null;
let streamList = [];
let itemSeq = 0;

export default (() => {

    const init = () => {
        $list_panel = $('#panel-join-screen');
        $contents = $list_panel.find('.contents');
        $btn_add = $list_panel.find('#btn-add-screen');
        // $toggle_ignore = $list_panel.find('#switch-join-video');

        $list = $('#join-screen-list');
        $list_count = $list_panel.find('.count-container').find('.count');

        $loading_anim = $('#panel-share-screen .contents .loading-anim');
        $loading_anim.hide();

        if ($.i18n && $.i18n.t) {
            $list.append(`<span class="empty-msg">${$.i18n.t('not_joined_screen')}</span>`);
        } else {
            $list.append(`<span class="empty-msg" data-i18n="not_joined_screen"></span>`);
        }
        bindEventHandler();
        // checkStreams();
    };

    const bindEventHandler = () => {
        $btn_add.click((e) => {
            e.preventDefault();
            if ($list.find('li').length === 3) {
                alert('화면 공유는 3개 이상 할 수 없습니다');
                return;
            }
            $('body').append(`<div id="popup-bg" class="popup-container"></div>`);
            navigator.mediaDevices.getDisplayMedia({
                video: true,
                audio: true
            })
            .then( stream => {
                addListItem(stream)
                $('#popup-bg').remove();
            })
            .catch( e => {
                console.log(e)
                $('#popup-bg').remove();
            })
        })
    }

    const addListItem = (stream) => {
        $list.find('.empty-msg').remove();
        $list.append(`
            <li class="card li-join-screen" data-item-seq="${itemSeq}">
                <div class="thumbnail-wrapper">
                    <video></video>
                </div>
                <div class="button-container">
                    <button class="btn-join btn rnd small">
                        ${i18next.t('join')}
                    </button>
                    <button class="btn-delete btn rnd small tr">
                        제거
                    </button>
                </div>
            </li>
        `)
        const $video = $(`.li-join-screen[data-item-seq="${itemSeq}"] video`);
        const videoEl = $video.get(0);
        videoEl.muted = true;
        videoEl.srcObject = stream;
        videoEl.play();

        streamList.push({
            seq: itemSeq.toString(),
            stream
        })

        itemSeq = itemSeq + 1;
        bindEventAllItems();
    }

    const deleteListItem = (itemSeq) => {
        const objID = $(`.li-join-screen[data-item-seq="${itemSeq}"]`).attr('data-obj-id');
        if(objID) {
            deleteItem(itemSeq)
        }
        $(`.li-join-screen[data-item-seq="${itemSeq}"]`).remove();

        const item = streamList.find(k => k.seq === itemSeq);
        item.stream.getTracks().forEach(track => track.stop());
        item.stream = undefined;
        const idx = streamList.findIndex( k => k.seq === itemSeq );
        streamList.splice(idx, 1);
    }

    const clear = () => {
        $list.empty();
        $list.addClass('empty');
        $list_count.text(0);
    };

    const bindEventAllItems = () => {
        $('.li-join-screen .thumbnail-wrapper').off();
        $('.li-join-screen .thumbnail-wrapper').click(async (e) => {
            e.preventDefault();
            const rootElement = $(e.currentTarget).closest('.li-join-screen');

            const itemSeq = rootElement.attr('data-item-seq');
            const item = streamList.find(k => k.seq === itemSeq);
            const stream = item.stream;
            const objID = rootElement.attr('data-obj-id');
            uiPopupVideoPlayer.open(
                stream,
                objID,
                item ? item.position_code : null,
                (position, code) => { addItem(itemSeq, stream, position, code)},
                (objID, position, code) => {updateItem(itemSeq, position, code)}
            )
        })

        $('.li-join-screen .btn-join').off();
        $('.li-join-screen .btn-join').click(async e => {
            e.preventDefault();
            const rootElement = $(e.currentTarget).closest('.li-join-screen');
            let itemSeq = rootElement.attr('data-item-seq');

            if (!$(e.currentTarget).hasClass('joined')) {
                const listItem = streamList.find(k => k.seq === itemSeq);
                let position = !listItem.position ? PRESET_POSITION.TOP_LEFT_SMALL : listItem.position;
                addItem(itemSeq, listItem.stream, position)
            } else {
                deleteItem(itemSeq)
            }
        });

        $('.li-join-screen .btn-delete').off();
        $('.li-join-screen .btn-delete').click(async e => {
            e.preventDefault();
            const rootElement = $(e.currentTarget).closest('.li-join-screen');
            let itemSeq = rootElement.attr('data-item-seq');

            deleteListItem(itemSeq);
        })


        // $('.li-join-video .title-container')
    }

    // 창을 닫아도 Track이 계속 오기때문에 체크가 안됨..
    // const checkStreams = () => {
    //     streamList.map( item => {
    //         if(item.stream.getTracks && item.stream.getTracks().length === 0) {
    //             deleteListItem(item.seq)
    //         }
    //     })
    //     _intVal_checkStream = setTimeout(checkStreams, 1000);
    // }
    // const stopCheckStreams = () => {
    //     clearTimeout(_intVal_checkStream);
    // }

    const addItem = async (
        itemSeq,
        stream,
        position = PRESET_POSITION.TOP_LEFT_SMALL,
        code = {
            screen: "SCREEN_SMALL",
            location: "TOP_LEFT"
        }
    ) => {
        StreamMixer.addMedia({
                source: stream,
                sourceType: 'stream',
                video: {
                    ...position,
                    zIndex: 1,
                    autoSize: true
                },
                audio: {
                    mediaType: 'media',
                    muted: false
                }
        })
        .then(res => {
            const objID = res.objID;
            let item = streamList.find(k => k.seq === itemSeq)
            item.objID = objID;
            item.position = position;
            item.position_code = code;
            // Object.assign(
            //     streamList.find(k => k.seq === itemSeq),
            //     { objID: res.objID, position, position_code: code }
            // );


            const rootEl = $(`.li-join-screen[data-item-seq="${itemSeq}"]`);
            rootEl.attr('data-obj-id', objID);

            rootEl.find('.btn-join').text($.i18n.t('cancel'));
            rootEl.find('.btn-join').addClass('tr');
            rootEl.find('.btn-join').addClass('joined');
        })
        .catch( err => {
            console.log(err)
        })

    }

    const updateItem = (
        itemSeq,
        position = PRESET_POSITION.TOP_LEFT_SMALL,
        code = {
            screen: "SCREEN_SMALL",
            location: "TOP_LEFT"
        }
    ) => {
        let item = streamList.find(k => k.seq === itemSeq);
        if (!item) return;
        item.position = position;
        item.position_code.screen = code.screen;
        item.position_code.location = code.location;
    }

    const deleteItem = (itemSeq) => {
        if (streamList.find(k => k.seq === itemSeq)) {
            delete streamList.find(k => k.seq === itemSeq)
        }
        const rootEl = $(`.li-join-screen[data-item-seq="${itemSeq}"]`);
        const objID = rootEl.attr('data-obj-id');
        StreamMixer.removeMedia(objID)

        rootEl.removeAttr('data-obj-id')

        rootEl.find('.btn-join').text($.i18n.t('join'));
        rootEl.find('.btn-join').removeClass('tr');
        rootEl.find('.btn-join').removeClass('joined');
    }


    return {
        init,
        clear
    };
})();

