let SNS_RELAY = [];
// 릴레이 설정
export const RELAY = {
    get: () => {
      return SNS_RELAY;
    },
    checkEmpty: () => {
      // return empty true
      if (!SNS_RELAY || (SNS_RELAY && SNS_RELAY.length === 0)) {
        return true
      } else {
        return false;
      }
      return true;

    },
    addRelay: (relay_info) => {
      var checkIndex = SNS_RELAY.findIndex(k => k.type === relay_info.vendor);
      if (checkIndex !== -1) {
        SNS_RELAY.splice(checkIndex, 1);
      }

      if(relay_info.vendor === "instagram") {
        SNS_RELAY.push({
          type: relay_info.vendor,
          data: {
            vendor: relay_info.vendor,
            protocol: relay_info.protocol ? relay_info.protocol : 'rtmp',
            id: relay_info.id,
            pass: relay_info.pw
          }
        })
      } else {
        SNS_RELAY.push({
          type: relay_info.vendor,
          data: {
            vendor: relay_info.vendor,
            protocol: relay_info.protocol ? relay_info.protocol : 'rtmp',
            url: relay_info.url.endsWith('/') ? relay_info.url : relay_info.url + '/',
            stream_key: relay_info.streamKey
          }
        });
      }
    },
    removeRelay: (vendor) => {
      var checkIndex = SNS_RELAY.findIndex(k => k.type === vendor);
      if (checkIndex !== -1) {
        SNS_RELAY.splice(checkIndex, 1);
      }

    }
}




