// 라이브 생성된 정보
const DEFAULT_EXPIRATION_TIME = 3600 * 24
let _LIVE_INFO = {
  id: "",
  owner: "",
  expirationTime: DEFAULT_EXPIRATION_TIME,
};

export let LIVE_INFO = {
  get: () => {
    return _LIVE_INFO;
  },
  set: ({id, owner, expTime, room_token}) => {
    Object.assign(_LIVE_INFO, {
      live_seq: id,
      owner: owner,
      expirationTime: expTime ? expTime : DEFAULT_EXPIRATION_TIME,
      room_token
    })

    return _LIVE_INFO
  },
  setFileInfo: (fileInfo) => {
    Object.assign(_LIVE_INFO, {thumbnail: fileInfo});
  },
  setTitle: (title) => {
    Object.assign(_LIVE_INFO, {title: title});
  },
  setDesc: (desc) => {
    Object.assign(_LIVE_INFO, {desc: desc});
  }
}


