export default (()=>{
    const SHARE_BASE_URL = 'https://cojam.tv';
    let $btn_share = null;
    let $menu = null;
    let _enabled = false;
    let _disabelMsg = "";
    let _url = "";
    let _text = "";
    let _imageUrl = "";

    const init = () => {
        $btn_share = $('#btn-share');
        $menu = $('#share-menu');
        // $menu.hide();
        bindEventHandler();
    }

    const setData = (url, text, imageUrl) => {
        _url = SHARE_BASE_URL + url;
        _text = text;
        if(!imageUrl) {
            _imageUrl = "https://cojam.tv/static/cojam/res/img-og-main.jpg";
        } else {
            _imageUrl = imageUrl;
        }
    }

    const setEnable = (enabled, disableMsg) => {
        _enabled = enabled;
        if(disableMsg) {
            _disabelMsg = disableMsg;
        }
    }

    const bindEventHandler = () => {
        $('html').click( (e) => {
            if($(e.target).is($btn_share) || $(e.target).parent().is($btn_share)) return;
            $menu.hide();
        })
        $btn_share.click( (e) => {
            e.preventDefault();
            if (!_enabled) {
                if(_disabelMsg) {
                    alert(_disabelMsg);
                } else {
                    alert($.i18n.t('wrng_retry_cast'));
                }
                return;
            }
            $menu.toggle();
        })
        $menu.find('.share-item').click( (e) => {
            const vendor = $(e.currentTarget).attr('data-share-vendor');
            if(vendor === 'twitter') {
                window.open(`https://twitter.com/intent/tweet?url=${_url}&text=${_text}`, '_blank')
            } else if(vendor === 'pinterest') {
                window.open(`http://pinterest.com/pin/create/link/?media=${_imageUrl}&description=${_text}&url=${_url}&`, '_blank');
            } else if(vendor === 'facebook') {
                window.open(`https://www.facebook.com/sharer.php?u=${_url}`);
            } else if(vendor === 'line') {
                window.open(`https://lineit.line.me/share/ui?url=${_url}&text=${_text}`);
            } else if(vendor === 'copy-link'){
                var t = document.createElement("textarea");
                document.body.appendChild(t);
                t.value = `${_url}`;
                t.select();
                document.execCommand('copy');
                document.body.removeChild(t);
            }
        })
    }

    return {
        init, setData, setEnable
    }


})()