import Fetch from '../utils/Fetch';
export const LIVE_STATE = {
    WAIT: "대기중",
    START: "방송중",
    END:   "종료"
}
export default (() => {

    // api서버에 라이브 생성
    const createLive = () => {
        // console.log("registration", sessionID, title, desc)
        return Fetch({
            url: `/api/v1/live/registration`,
            type: 'POST',
            cache: false
        })
    }

    const updateLiveInfo = (liveSeq, title, desc) => {
        return Fetch({
            url: `/api/v1/live`,
            type: 'PUT',
            data: JSON.stringify({
                live_seq: liveSeq,
                title,
                description: desc
            })
        })
    }

    // 라이브 방송 상태를 '방송중' 으로 업데이트한다.
    // 라이브 방송 room_token을 업데이트한다.
    const setLiveStatus = (state, live_seq, room_token) => {
        if(state === LIVE_STATE.START) {
            return Fetch({
                url: "/api/v1/live/start",
                type: 'PUT',
                data: JSON.stringify({
                    live: {
                        live_seq,
                        room_token
                    }
                })
            });
        } else if (state === LIVE_STATE.END) {
            // 라이브 방송 상태를 '방송종료' 로 업데이트 한다.
            return Fetch({
                url: "/api/v1/live/end",
                type: 'PUT',
                data: JSON.stringify({
                    live: {
                        live_seq
                    }
                })
            });
        }
    };

    // 채널 관련
    // 채널 정보 가져오기
    const getChannelInfo = (userSeq) => {
        return Fetch({
            url: `/api/v1/channel/${userSeq}`,
            type: 'GET'
        })
    }

    const updateChannelInfo = (channelSeq, title, desc) => {
        return Fetch({
            url: `/api/v1/channel`,
            type: 'PUT',
            data: JSON.stringify({
                channel_seq: channelSeq,
                title,
                desc
            })
        })
    }

    const getUploadThumbnailUrl = (liveSeq, fileName, fileSize) => {
        return Fetch({
            url: `/api/v1/live/thumbnail`,
            type: 'POST',
            data: JSON.stringify({
                live_thumbnail: {
                    live_seq: liveSeq
                },
                file_upload_vo: {
                    upload_service_type: "live_thumbnail",
                    file_type: "image",
                    file_name: fileName,
                    content_length: fileSize
                }
            })
        })
    }

    const uploadThumbnailImg = (url, data, progressEvent) => {
        return new Promise((resolve, reject) => {
            let xhr = null;
            xhr = $.ajax({
                url,
                type: 'PUT',
                headers: {
                    'x-amz-acl': 'authenticated-read'
                    // ...header,
                    // ...meta
                },
                contentType: 'binary/octet-stream',
                processData: false,
                data,
                xhr: function () {
                    var xhr = new window.XMLHttpRequest();
                    //Upload progress
                    if (progressEvent) {
                        xhr.upload.addEventListener("progress", function (evt) {
                            if (evt.lengthComputable) {
                                var percentComplete = (evt.loaded / evt.total);
                                progressEvent(percentComplete);
                            }
                        }, false);
                    }
                    return xhr;
                },
                success: function (res) {
                    xhr = null;
                    return resolve(res);
                },
                error: function (err) {
                    xhr = null;
                    console.log("upload error")
                    console.log(err)
                    return reject(err);
                }
            })

        })

    }


    // 시청자 수 가져오기
    const getChannelCountInfo = (liveSeq) => {
        try {
            return Fetch({
                url: `/api/v1/live/info/${liveSeq}`
            })
        } catch (err) {
            console.log("채널 정보를 불러오는데 실패했습니다.")
        }
    };


    // 참여 관련
    const getJoinList = (liveSeq) => {
        return Fetch({
            url: `/api/v1/live/join/list/${liveSeq}`,
            type: 'GET',
            cache: false,
        })
        
    }

    const setIgnoreJoin_Live = (channelSeq, value) => {
        return Fetch({
            url: `/api/v1/channel/livejoin`,
            type: 'PUT',
            data: JSON.stringify({
                channel_seq: channelSeq,
                live_join_yn: value
            })
        })
    }

    const clearJoinList_Live = (liveSeq, channelSeq) => {
        if(!liveSeq) { console.log("not live info"); return false;}
        if(!channelSeq) { console.log("not channel info"); return false;}
        return Fetch({
            url: `/api/v1/live/join/init`,
            type: 'DELETE',
            data: JSON.stringify({
                live_seq: liveSeq,
                channel_seq: channelSeq
            })
        })
    }

    const getJoinList_Video = (channelSeq, start, length) => {
        return Fetch({
            url: `/api/v1/vod/join/channel/${channelSeq}/${length}/${start}`,
            type: 'GET',
            cache: false,
        })

    }

    const setIgnoreJoin_Video = (channelSeq, value) => {
        return Fetch({
            url: `/api/v1/channel/vodjoin`,
            type: 'PUT',
            data: JSON.stringify({
                channel_seq: channelSeq,
                vod_join_yn: value
            })
        })
    }

    const clearJoinList_Video = (channelSeq) => {
        return Fetch({
            url: `/api/v1/vod/join/${channelSeq}`,
            type: 'DELETE'
        })
    }


    const cancelJoin = (liveSeq, userSeq) => {
        return Fetch({
            url: `/api/v1/live/join/${liveSeq}/${userSeq}`,
            type: 'DELETE'
        })
    }


    const createRoom_P2P = () => {
        return Fetch({
            url: `/api/v1/live/room/make/P2P`
        });
    }


    const getLiveList = (start, count, order) => {
        return Fetch({
            url: `/api/v1/live?status=1003&length=${count}&start=${start}&order=${order}`,
            type: 'GET',
            cache: false
        })
    }


    const getRecommendLive = (userSeq, start, count) => {
        return Fetch({
            url: `/api/v1/live/relation/${userSeq}/${count}/${start}`
        })
    }


    const sendReport = (text) => {
        return Fetch({
            url: 'https://hooks.slack.com/services/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',
            type: 'POST',
            data: JSON.stringify({text}),
            contentType: 'text/plain'
        })
    }

    const sendReportTest = (obj) => {
        return Fetch({
            url: 'https://hooks.slack.com/services/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',
            type: 'POST',
            data: JSON.stringify({text:"안녕하세요"}),
            contentType: 'text/plain'
        })
    }


    return {
        createLive, updateLiveInfo, setLiveStatus,
        getChannelInfo, updateChannelInfo, getUploadThumbnailUrl, uploadThumbnailImg, getChannelCountInfo,
        getJoinList, setIgnoreJoin_Live, clearJoinList_Live,
        getJoinList_Video, setIgnoreJoin_Video, clearJoinList_Video,
        cancelJoin,
        createRoom_P2P,
        getLiveList, getRecommendLive,
        sendReport, sendReportTest
    }
})()
