export default (() => {
  let $my_camera_list = null;
  let _current_device = null;
  let _deviceList = [];

  const init = async () => {
    return await initDevice();
  };

  const initDevice = async () => {
    _deviceList = await getAvailableDeviceList();
    const { audio, video } = _deviceList;
    
    if(video.length === 0 ) {
      alert($.i18n.t('wrng_device_not_found'));
      return;
    }

    return autoSelectStream()
  };

  // 그룹으로 매칭되는 디바이스가 있으면 해당 디바이스의 stream을 리턴 (_deviceList의 video를 기준으로 loop)
  const autoSelectStream = () => {
    const { audio, video } = _deviceList;
    // 똑같은 그룹을 가진 디바이스가 있으면 리턴
    for (const v of video) {
      if (v.groupId) {
          const groupAudio = findGroupAudio(v.groupId);
          if(groupAudio) {
            setCurrentDevice({video: v, audio: groupAudio});
            return getWebcamStream(v.deviceId, groupAudio.deviceId)
          }
      }
    }

    // 똑같은 그룹의 디바이스가 하나도 없으면, 첫번째 장치끼리 묶어서 보냄 (video, audio 서로의 장치가 다를 수 있음)
    setCurrentDevice({video: video[0], audio: audio[0]});
    return getWebcamStream(video[0].deviceId, audio[0].deviceId);
  }

  const findGroupAudio = (groupId) => {
    const { audio } = _deviceList;
    const groupAudio = audio.find(k => k.groupId === groupId);
    if (groupAudio) {
      return groupAudio;
    }
  }

  const setCurrentDevice = (val) => {
    _current_device = val;
  }

  const getCurrentDevice = () => {
    return _current_device;
  }

  const getWebcamStream = async (videoID, audioID) => {
    const constraints = {
      audio: false,
      video: false
    };

    if (audioID) {
      constraints.audio = {};
      constraints.audio.deviceId = { exact: audioID }
      constraints.audio.echoCancellation = true,
      constraints.audio.noiseSuppression = true
    }

    constraints.video = {};
    constraints.video.deviceId = { exact: videoID };
    constraints.video.width = { min: 640, ideal: 1280 };
    constraints.video.height = { min: 360, ideal: 720 };
    constraints.video.frameRate = 30;
    const userMedia = await navigator.mediaDevices.getUserMedia(constraints);

    return userMedia;
  }

  const getDeviceList = () => {
    return _deviceList;
  }

  // output 장치를 제외한 각 장치를 테스트해보고, 사용가능한 장치 목록을 뽑아줌
  const getAvailableDeviceList = async () => {
    const devices = await navigator.mediaDevices.enumerateDevices();
    if(devices.length === 0) return false;

    const videoDevices = [];
    const audioDevices = [];
    for(const device of devices) {
      const { deviceId, groupId, label, kind } = device;
      if(kind !== 'videoinput' && kind !== 'audioinput')
        continue;
      if (deviceId.toLowerCase() === "default")
        continue;

      if (isCamera(kind)) {
        const name = label || `CAM ${videoDevices.length}`;
        try {
          const stream = await getTestStream(deviceId, kind);
          videoDevices.push({ deviceId, groupId, name, kind, init: true });
          stream.getTracks().forEach(track => track.stop());
        }
        catch (err) {
          console.log(err)
          videoDevices.push({ deviceId, groupId, name, kind, init: false });
        }
      }
      else {
        const name = label || `MIC ${audioDevices.length}`;
        try {
          const stream = await getTestStream(deviceId, kind);
          audioDevices.push({ deviceId, groupId, name, kind, init: true });
          stream.getTracks().forEach(track => track.stop());
        }
        catch (err) {
          console.log(err)
          audioDevices.push({ deviceId, groupId, name, kind, init: false });
        }
      }
    }

    return {
      audio: audioDevices.filter(audio => audio.init === true),
      video: videoDevices.filter(video => video.init === true)
    }
  };

  const isCamera = kind => kind === 'videoinput';

  const getTestStream = (deviceId, kind) => {
    return isCamera(kind)
        ? getTestVideoStream(deviceId)
        : getTestAudioStream(deviceId);
  }

  const getTestAudioStream = deviceId => {
    return navigator.mediaDevices.getUserMedia({
      audio: {
        deviceId: {
          exact: deviceId,
        },
        echoCancellation: true,
        noiseSuppression: true
      },
      video: false
    });
  };

  const getTestVideoStream = deviceId => {
    return navigator.mediaDevices.getUserMedia({
      audio: false,
      video: {
        width: { exact: 1280 },
        height: { exact: 720 },
        deviceId: { exact: deviceId }
      }
    });
  };


  return {
    init,
    getDeviceList,
    getWebcamStream,
    findGroupAudio,
    getCurrentDevice
  }


})();




