import Popup from '../../../common_ui/popup';

import { CHANNEL_INFO, LIVE_INFO, LIVE_STATUS } from '../store/live.store';

// service
import LiveService from '../../../services/live.service';

const MAX_TITLE_LENGTH = 30;
const MAX_DESC_LENGTH = 200;
export default (() => {
  let $btn_channel_settings = null;
  let $popup_channel_settings = null;
  
  let $input_file_thumbnail = null;
  let $img_thumbnail_preview = null;
  let $thumbnail_info = null;
  let selectFile = null;
  let fileInfo = null;
  let origin_file = null;

  let $input_title = null;
  let origin_title = "";
  let $lbl_title_length = null;
  let $input_desc = null;
  let $lbl_desc_length = null;
  let origin_desc = "";
  let $btn_confirm = null;
  let $btn_cancel = null;

  const init = () => {
    $btn_channel_settings = $('#btn-live-channel-settings');
    $popup_channel_settings = $('#popup-channel-settings');

    $input_file_thumbnail = $popup_channel_settings.find('#input-file-main-thumbnail');
    $img_thumbnail_preview = $popup_channel_settings.find('#img-thumbnail-preview');
    $thumbnail_info = $popup_channel_settings.find('#lbl-thumbnail-info');

    $input_title = $popup_channel_settings.find('#input-channel-title');
    $input_desc = $popup_channel_settings.find('#input-channel-desc');
    $lbl_title_length = $('.title-container .counter span');
    $lbl_desc_length = $('.desc-container .counter span');

    $btn_confirm = $popup_channel_settings.find('.btn-popup-ok');
    $btn_cancel = $popup_channel_settings.find('.btn-popup-cancel');

    bindEventHandler();
  }

  const bindEventHandler = () => {
    $btn_channel_settings.click((e) => {
      if(!e.ctrlKey && !e.metaKey) {
        Popup.open($popup_channel_settings);
        origin_title = $input_title.val();
        origin_desc = $input_desc.val();
      }
    });

    $input_title.on('propertychange change keyup paste input', () => {
      const inputText = $input_title.val();

      // 입력이 제한 길이보다 길면 자른다.
      if (inputText.length > MAX_TITLE_LENGTH) {
        $input_title.val(inputText.substring(0, MAX_TITLE_LENGTH));
        setTitleLength(MAX_TITLE_LENGTH);
      }

      setTitleLength(inputText.length);
    });

    $input_desc.on('propertychange change keyup paste input', () => {
      const inputText = $input_desc.val();

      // 입력이 제한 길이보다 길면 자른다.
      if (inputText.length > MAX_DESC_LENGTH) {
        $input_desc.val(inputText.substring(0, MAX_DESC_LENGTH));
        setDescLength(MAX_DESC_LENGTH);
      }

      setDescLength(inputText.length);
    });


    $input_file_thumbnail.change( async (e) => {
      e.preventDefault();
      if (!$(e.target).get(0) || !$(e.target).get(0).files[0]) {
        selectFile = false;
        return false;
      }
      fileInfo = $(e.target).get(0).files[0];

      if (fileInfo.type !== "image/png" && fileInfo.type !== "image/jpeg") {
        alert($.i18n.t('sel_file_png_jpeg'));
        return false;
      }

      // const resGetUploadUrl = await AccountService.getUploadProfileUrl(
      //   USER_SESSION.user_seq,
      //   fileInfo.name,
      //   fileInfo.size
      // );
      let reader = new FileReader();
      reader.onload = function (e) {
        $img_thumbnail_preview.attr('src', e.target.result)
        $img_thumbnail_preview.show();
      };

      reader.readAsDataURL(fileInfo);
      selectFile = true;
    })
    
    $btn_confirm.click( async () => {
      const title = $input_title.val();
      const desc = $input_desc.val();
      if (title.length === 0) {
        alert($.i18n.t('wrng_enter_title'));
        return;
      }
      if(selectFile) {
        LIVE_INFO.setFileInfo(fileInfo);
      }
      Popup.close();
    });

    $('*:disabled').click( (e) => {
      $popup_channel_settings.hide();
      alert($.i18n.t('wrng_not_settings'), () => {
        $popup_channel_settings.show();
      })
    })
  }

  const initData = () => {
    // 채널설정 초기화
    // 채널 정보 받아온 후 적용
    if(CHANNEL_INFO.get().title) {
      $input_title.val(CHANNEL_INFO.get().title);
      setTitleLength(CHANNEL_INFO.get().title.length);

    } else {
      $input_title.val(`${USER_SESSION.nick_name}의 생방송입니다`);
    }

    if(CHANNEL_INFO.get().description) {
      $input_desc.val(CHANNEL_INFO.get().description);
      setDescLength(CHANNEL_INFO.get().description.length);
    }

    // $.ajax({
    //     url: "/api/v1/channel/1",
    //     type: 'GET'
    // }).then( res => {
    //     console.log(res)
    // })
  };

  const sendSettings = async () => {
    LiveService.updateChannelInfo(
      CHANNEL_INFO.get().channel_seq,
      $input_title.val(),
      $input_desc.val()
    )
    .then(res => {
    })
    .catch(err => {
      console.log(err)
      $input_title.val(origin_title);
      $input_desc.val(origin_desc);
    })

    if(LIVE_INFO.get().thumbnail) {
      const resGetUploadUrl = await LiveService.getUploadThumbnailUrl(
        LIVE_INFO.get().live_seq,
        LIVE_INFO.get().thumbnail.name,
        LIVE_INFO.get().thumbnail.size
      );
      const { url } = resGetUploadUrl.data.info;
      await LiveService.uploadThumbnailImg(url, LIVE_INFO.get().thumbnail, (percent) => { console.log(percent) })
    }
  }

  const setEnableAll = (enabled) => {
    $input_title.attr('disabled', !enabled);
    $input_desc.attr('disabled', !enabled);
    $input_file_thumbnail.attr('disabled', !enabled);
  }

  const setTitleLength = (length) => {
    $lbl_title_length.text(length)
  }

  const setDescLength = (length) => {
    $lbl_desc_length.text(length)
  }

  return {
    init, initData, sendSettings, setEnableAll
  }
})()