
import { LIVE_STATUS, RELAY } from '../store/live.store';
import Popup from '../../../common_ui/popup'
const RelayPopup = (() => {

    let $popup_fb = null;
    let $popup_yt = null;
    let $popup_insta = null;
    let $btn_open_fb = null;
    let $btn_open_yt = null;
    let $btn_open_insta = null;
    let $btn_confirm_fb = null;
    let $btn_confirm_yt = null;
    let $btn_confirm_insta = null;

    const init = (onConfirmRelay) => {
        $popup_fb = $('#popup-fb');
        $popup_yt = $('#popup-yt');
        $popup_insta = $('#popup-insta');

        $btn_open_fb = $('#btn-open-fb');
        $btn_open_yt = $('#btn-open-yt');
        $btn_open_insta = $('#btn-open-insta');

        $btn_confirm_fb = $popup_fb.find('.btn-popup-ok');
        $btn_confirm_yt = $popup_yt.find('.btn-popup-ok');
        $btn_confirm_insta = $popup_insta.find('.btn-popup-ok');

        $popup_fb.hide();
        $popup_yt.hide();
        $popup_insta.hide();

        bindEventHandler();
    };
    

    const bindEventHandler = () => {
        $('#ic-multistream-info').click( e => {
            location.href = '/help'
        })
        //
        // SNS 릴레이 설정
        //
        // open popup
        $btn_open_fb.click(() => {
            if (LIVE_STATUS.get()) {
                alert($.i18n.t('wrng_no_setting'));
                return;
            }
            Popup.open($popup_fb)
        });
        $btn_open_yt.click(() => {
            if (LIVE_STATUS.get()) {
                alert($.i18n.t('wrng_no_setting'));
            return;
            }
            Popup.open($popup_yt)
        });
        $btn_open_insta.click(() => {
            if (LIVE_STATUS.get()) {
                alert($.i18n.t('wrng_no_setting'));
            return;
            }
            Popup.open($popup_insta)
        });
        // close popup
        // 팝업 클릭시에는 Close가 되지 않게(배경클릭시에만 Close)
        $('.multistream-popup').children('.popup').click(e => e.stopPropagation());
        $popup_fb.click(() => { Popup.close() })
        $popup_yt.click(() => { Popup.close() })
        $popup_insta.click(() => { Popup.close() })
    
    
        $btn_confirm_insta.click(() => {
            setSNSRelay($popup_insta);
        });
    
        // confirm facebook popup
        $btn_confirm_fb.click(() => {
            setSNSRelay($popup_fb);
        });
    
        // confirm youtube popup
        $btn_confirm_yt.click(() => {
            setSNSRelay($popup_yt);
        });
    };

    const setSNSRelay = ($popup) => {
        let vendor = $popup.attr("data-vendor");
        // vendor가 인스타일때는 id/pw 필요함.
        if (vendor === "instagram") {
            var id = $popup.find('.input-ms-id').val();
            var pw = $popup.find('.input-ms-pw').val();

            if(!id || !pw || id.length === 0 || pw.length === 0) {
                confirm(i18next.t('wrng_not_input_id_pw'), () => {
                    RELAY.removeRelay(vendor)
                    Popup.close()
                    $btn_open_insta.removeClass('on');
                });
            } else {
                RELAY.addRelay({ vendor: "instagram", id, pw })
                Popup.close()
                $btn_open_insta.addClass('on');
            }

        } else {
            var url = $popup.find('.input-ms-url').val();
            var streamKey = $popup.find('.input-ms-stream-key').val();
            if(!url || !streamKey || url.length === 0 || streamKey.length === 0) {
                confirm(i18next.t('wrng_not_input_key'), () => {
                    RELAY.removeRelay(vendor)
                    Popup.close();
                });
                if (vendor === "facebook") {
                    $btn_open_fb.removeClass('on');
                } else {
                    $btn_open_yt.removeClass('on');
                }
            } else {
                RELAY.addRelay({ vendor, url, streamKey })
                Popup.close();
                if (vendor === "facebook") {
                    $btn_open_fb.addClass('on');
                } else {
                    $btn_open_yt.addClass('on');
                }

            }

        }
    
      }
    return {
        init
    }
})()

export default RelayPopup;